import { Injectable } from '@angular/core';
import { Observable, from } from 'rxjs';
import { map } from 'rxjs/operators';

import { COMMON, LOGOUT_PARAMS } from './../utils/service-constant';
import { BaseConnectionService } from './base-connection.service';
import { PayloadCommandService } from './payload-command.service';
import { Action } from '../models/action';
import { APIEndPoints } from '../models/api-end-points.enum';
import { HttpVerbs } from '../models/http-verbs';

@Injectable({
    providedIn: 'root'
})
export class LogoutService {
    constructor(private payloadCommandService: PayloadCommandService,
        private baseConnectionService: BaseConnectionService) {

    }

    logout(): Observable<any> {
        // const that = this;
        // const moduleInfo = {
        //     moduleName: 'NEWPLANTAB'
        // };
        // const logoutPayload = that.payloadCommandService.getCommandDetails(LOGOUT_PARAMS.commandName, null, null, null, moduleInfo);
        // return that.baseConnectionService.sendRequest(logoutPayload).pipe(map((resp) => {
        //     if (resp.result === COMMON.RESULT_OK) {
        //         sessionStorage.clear();
        //     }
        //     return resp;
        // }));
        const payload = this.payloadCommandService.getApiRequest(LOGOUT_PARAMS.commandName, null, null, null);
        const actionParam: Action = {};
        actionParam.url = APIEndPoints.LOGOUT; // compulsory
        actionParam.isSSOEndPoint = true;
        actionParam.httpMethod = HttpVerbs.get;
        actionParam.prelogin = false; // passing withCredential true
        return this.baseConnectionService.send(payload, actionParam).pipe(map((resp) => {
            if (resp.result === COMMON.RESULT_OK) {
                sessionStorage.clear();
            }
            return resp;
        }));

    }
}
