import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewEncapsulation,
  OnChanges,
  SimpleChanges,
  ViewChild,
  ElementRef
} from '@angular/core';
import {
  FormControl,
  FormGroup,
  FormBuilder,
  Validators
} from '@angular/forms';
import { Observable, of, Subject, Subscription } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  map,
  mergeMap,
  startWith
} from 'rxjs/operators';

import { SearchListInput } from './../../models/search-list-input';
import { DEBOUNCE_TIME } from './../../utils/service-constant';
import { DialogService } from '../../services/dialog.service';
import { MatAutocompleteTrigger } from '@angular/material';

@Component({
  selector: 'ctos-search-list',
  templateUrl: './search-list.component.html',
  encapsulation: ViewEncapsulation.None
})
export class SearchListComponent implements OnInit, OnChanges, OnDestroy {
  @Input()
  isSearchListOn: Boolean;
  @Input()
  searchListInput: SearchListInput;
  @Input()
  required: Boolean;
  searchListFormGroup: FormGroup;
  @Input()
  set searchListFormGroupName(value: FormGroup) {
    this.searchListFormGroup = value;
  }
  get searchListFormGroupName(): FormGroup {
    return this.searchListFormGroup;
  }
  @Input() labels: {
    required?: string;
  } = {};
  @Output()
  itemSelect: EventEmitter<any>;
  @Output()
  selectionChange: EventEmitter<any>;
  @ViewChild('searchbox', { static: false }) searchbox: ElementRef;
  recentlistItemsSubject: Subject<any[]>;
  allListItemsSubject: Subject<any[]>;
  isFirstFilterCall: boolean;
  subscriptions: Map<string, Subscription>;
  toggleArrowFlag = false;

  /**
   * Creates an instance of search list component.
   * @param searchListFB
   * @param dialogService
   */
  constructor(
    private searchListFB: FormBuilder,
    private dialogService: DialogService
  ) {
    this.itemSelect = new EventEmitter();
    this.selectionChange = new EventEmitter();
  }

  private searchGroups: Array<any>;

  /**
   * Getter $searchGroups
   * @return Array<any>
   */
  public get $searchGroups(): Array<any> {
    return this.searchGroups;
  }

  ngOnInit() {
    this.subscriptions = new Map<string, Subscription>();
    this.initializeSubjects();
    this.setFilter();
    this.setSearchGroups();
  }

  /**
   * on changes
   * @param changes
   */
  ngOnChanges(changes: SimpleChanges) {
    if (
      changes &&
      changes.searchListFormGroupName &&
      this.searchListFormGroupName.get('searchListControl') &&
      this.searchListFormGroupName.get('searchListControl').value
    ) {
      this.removeSubscription();
      this.subscriptions = new Map<string, Subscription>();
      this.setFilter(
        this.searchListFormGroupName.get('searchListControl').value
      );
    }
  }

  /**
   * Gets selected item
   * @param selectedItemkey
   * @param list
   * @returns selected item
   */
  private getSelectedItem(selectedItemkey, list): any {
    let selectedItem;
    if (
      selectedItemkey !== undefined &&
      selectedItemkey !== null &&
      list !== undefined &&
      list !== null
    ) {
      selectedItem =
        list.find(item => {
          if (typeof selectedItemkey === 'string') {
            return (
              item.id.toLowerCase() === <string>selectedItemkey.toLowerCase()
            );
          } else {
            return item.key === selectedItemkey;
          }
        }) || '';
    } else {
      selectedItem = '';
    }
    if (
      this.dialogService.selectedStudyKey === undefined ||
      this.dialogService.selectedStudyKey === null ||
      this.dialogService.selectedStudyKey === ''
    ) {
      this.dialogService.selectedStudyKey = selectedItem;
    }
    return selectedItem;
  }

  private initializeSubjects() {
    this.recentlistItemsSubject = new Subject<any[]>();
    this.allListItemsSubject = new Subject<any[]>();
  }

  /**
   * Setter setSearchGroups
   */
  public setSearchGroups() {
    if (
      this.recentlistItemsSubject === undefined ||
      this.allListItemsSubject === undefined
    ) {
      this.initializeSubjects();
    }
    // tslint:disable-next-line:prefer-const
    let searchGroups = [];
    // recentListItemsLabel condition is checked for not showing most recently list In copy plan
    if (this.searchListInput.recentListItemsLabel) {
      searchGroups.push({
        name: this.searchListInput.recentListItemsLabel,
        list: this.recentlistItemsSubject.asObservable()
      });
    }
    searchGroups.push({
      name: this.searchListInput.allListItemsLabel,
      list: this.allListItemsSubject.asObservable()
    });
    this.searchGroups = searchGroups;
  }



  /**
   * Sets filter
   * @param [initialValue]
   */
  private setFilter(initialValue?: string) {
    this.isFirstFilterCall = true;
    if (!this.subscriptions.has('valueChangeSubscription')) {
      const listControl = this.isSearchListOn ? 'filterListControl' : 'searchListControl';
      if (this.searchListFormGroupName
        .get(listControl)) {
        const valueChangeSubscription = this.searchListFormGroupName
          .get(listControl)
          .valueChanges.pipe(
            startWith<string | any>(initialValue || ''),
            debounceTime(DEBOUNCE_TIME),
            map(value => {
              return typeof value === 'string' ? value : value.id;
            }),
            distinctUntilChanged((oldValue, newValue) => {
              return oldValue === newValue;
            }),
            mergeMap(item => {
              return this.filterItems(
                item,
                initialValue || this.searchListInput.selectedItemKey
              );
            })
          )
          .subscribe(data => { });
        this.subscriptions.set(
          'valueChangeSubscription',
          valueChangeSubscription
        );
      }
    }
  }

  /**
   * Filters items
   * @param value
   * @param [selectedItemKey]
   * @returns items
   */
  private filterItems(
    value: string,
    selectedItemKey?: number | string
  ): Observable<any[]> {
    const self = this;
    if (self.searchListInput.searchApi !== undefined) {
      const searchApiSubscription = self.searchListInput
        .searchApi(value)
        .subscribe(resp => {
          if (resp.allListItems && resp.allListItems.length > 0) {
            self.recentlistItemsSubject.next(resp.recentListItems);
            self.allListItemsSubject.next(resp.allListItems);
          } else if (resp.allListItems && resp.allListItems.length <= 0) {
            self.recentlistItemsSubject.next([this.searchListInput.recentListItemsLabel]);
            self.allListItemsSubject.next([this.searchListInput.allListItemsLabel]);
          }
          const selectedItem: any = self.getSelectedItem(selectedItemKey, resp.recentListItems);
          if (this.isFirstFilterCall) {
            self.searchListFormGroupName
              .get('searchListControl')
              .setValue(selectedItem);
            this.isFirstFilterCall = false;
          }
          this.selectionChange.emit(selectedItem);
        });
      this.subscriptions.set('searchApiSubscription', searchApiSubscription);
    }
    return of(self.searchGroups);
  }

  /**
   * Formats item
   * @param [item]
   * @returns item
   */
  formatItem(item?: any): string | undefined {
    let ret: string;
    if (!item) {
      return;
    } else if (typeof item === 'string') {
      ret = item;
    } else {
      ret = item.id;
    }
    return ret;
  }

  /**
   * Clicks search list component
   */
  click() {
    this.searchListFormGroup.get('filterListControl').setValue('');
  }

  /**
   * Determines whether blur on
   */
  onBlur() {
    let newValue = '';
    if (this.searchListFormGroup.get('searchListControl') &&
      this.searchListFormGroup.get('searchListControl').value) {
      if (this.searchListFormGroup.get('searchListControl').value.hasOwnProperty('searchListControl')) {
        newValue = this.searchListFormGroup.get('searchListControl').value.searchListControl;
      } else {
        newValue = this.searchListFormGroup.get('searchListControl').value;
      }
    }
    this.searchListFormGroup.get('searchListControl').setValue(newValue);
  }

  /**
   * Opens search list component
   */
  open() {
    this.toggleArrowFlag = true;
    if (this.searchListFormGroup.get('filterListControl') &&
      this.searchListFormGroup.get('filterListControl').value) {
      this.searchListFormGroup.get('filterListControl').setValue('');
    }
  }

  /**
   * Closes search list component
   */
  close() {
    if (this.searchListFormGroup.get('filterListControl') &&
      this.searchListFormGroup.get('filterListControl').value) {
      this.searchListFormGroup.get('filterListControl').setValue('');
    }
    this.toggleArrowFlag = false;
  }

  /**
   * Items selected
   * @param event
   */
  itemSelected(event) {
    const isReset =
      this.dialogService.isProceed !== undefined
        ? this.dialogService.isProceed
        : false;
    this.canDeactivate(isReset).subscribe(res => {
      if (res === true) {
        this.dialogService.isStatusChanged = false;
        this.selectionChange.emit(event.option.value);
        this.itemSelect.emit(event);
        this.searchbox.nativeElement.expanded = false;
        this.searchbox.nativeElement.blur();
      } else {
        const values = this.dialogService.selectedStudyKey;
        this.searchListFormGroupName.get('searchListControl').setValue(values);
      }
    });
  }

  /**
   * Determines whether deactivate can
   * @param [isReset]
   * @returns deactivate
   */
  canDeactivate(isReset?: boolean): Observable<boolean> {
    if (isReset === true) {
      return of(true);
    }
    if (this.dialogService.isStatusChanged === true) {
      return this.dialogService.confirm();
    }
    return of(true);
  }

  ngOnDestroy(): void {
    this.searchListInput = null;
    this.itemSelect = null;
    this.selectionChange = null;
    this.searchListFormGroupName = null;
    this.recentlistItemsSubject = null;
    this.allListItemsSubject = null;
    this.searchGroups = null;
    this.removeSubscription();
  }

  /**
   * Removes subscription
   */
  removeSubscription() {
    this.subscriptions.forEach(subscription => {
      subscription.unsubscribe();
    });
    this.subscriptions = null;
  }
}
