import { Injectable } from '@angular/core';
import { HttpEvent, HttpInterceptor, HttpHandler, HttpRequest, HttpClient, HttpResponse } from '@angular/common/http';
import { environment } from 'environments/environment';
import { from, Observable, throwError } from 'rxjs';
import { catchError, map, finalize } from 'rxjs/operators';
import { ErrorTermsStoreService } from 'common-library';
import { ErrorMessageService, GlobalErrorHandler, LoadingScreenService } from 'so-common-library';
import { ZERO } from './app.constant';

@Injectable()   // { providedIn: 'root' }
export class AuthInterceptor implements HttpInterceptor {

    constructor(
      private http: HttpClient,
      private loadingScreenService: LoadingScreenService,
      private errorMessageService: ErrorMessageService,
      private errorTermsStoreService: ErrorTermsStoreService
    ) { }

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

      req = req.clone({
        withCredentials: true
      });

      if (environment.production) {

        return next.handle(req).pipe(catchError((error, caught) => {

          // intercept the response error and display it in console
          console.log('Error Occurred', error);

          this.loadingScreenService.stopLoading();

          // return the error to the method that called it
          // TODO move error in new error inteceptor
          if (error && error.status === ZERO) {
            const globalErrorHandler = new GlobalErrorHandler(this.errorTermsStoreService, this.errorMessageService);
            globalErrorHandler.handleError(error);
          }
          return throwError(error);

        }) as any, finalize(() => {
          this.loadingScreenService.stopLoading();
        }));
      } else {
        // get command name - ex- getPlanDetails

        const params = req.params;
        /* console.log('Params : ', params.toString());
        console.log('Params : ', params.get('category')); */

        const parameter = params.get('category');
        const commandName = req.headers.get('command');

        if (!commandName) {
          return next.handle(req);
        }

        // Fetch the respective static json and return as an observable
        return from(this.http.get('./assets/data/' + commandName + '.json').pipe(map((data) => {
          let responseData;
          // If the static json consists of parameter nodes then fetch and send the respective node
          // else send the complete json as respnse
          if (parameter) {
            if (data.hasOwnProperty(parameter)) {
              responseData = data[parameter];
            }
          } else {
            responseData = data;
          }
          // Wrap the json in HttpResponse object
          return new HttpResponse({ status: 200, body: responseData });
        })));
      }
    }
}
