import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';

import { ResetPassword } from '../../models/reset-password';
import { ErrorTermsStoreService } from '../../services/error-terms-store.service';
import { ResetPasswordService } from '../../services/reset-password.service';

@Component({
  selector: 'ctos-password',
  templateUrl: './password.component.html'
})
export class PasswordComponent implements OnInit {
  // count = 1;
  // passwordRules: String;
  currentPassword: string;
  newPassword: string;
  confirmPassword: string;
  // passWordPolicyList: Array<string> = ['LENGHTHRULE', 'MIXCHARRULE'];

  // nonEmptyField: string;
  // passwordMisMatchMessage: string;
  validationMessage = '';
  // pointsSplit: string[];
  // mixruleSplitPoints: string[];
  mixruleSplit: string[];
  mixruleSplitHeader: string;
  // rules: string[];
  showSuccessMessage = false;
  showError = false;

  /**
   * Creates an instance of password component.
   * @param dialogRef
   * @param data
   * @param resetPasswordService
   * @param errorTermsStoreService
   */
  constructor(
    public dialogRef: MatDialogRef<PasswordComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private resetPasswordService: ResetPasswordService,
    private errorTermsStoreService: ErrorTermsStoreService
  ) {
    if (!data.okLabel) {
      data.okLabel = 'Ok';
    }
    if (!data.cancelLabel) {
      data.cancelLabel = 'Cancel';
    }
  }

  ngOnInit() {
    this.getPasswordRulesList();
  }

  /**
   * Cancels password component
   * @param btnLabel
   */
  cancel(btnLabel) {
    this.dialogRef.close();
  }

  /**
   * password component
   * @param btnLabel
   * @param form
   */
  ok(btnLabel, form) {
    if (form.valid) {
      const passowordInfo: ResetPassword = {
        currentPassword: form.value.currentPassword,
        newPassword: form.value.newPassword
      };
      this.changePassword(passowordInfo);
    }
  }

  /**
   * Changes password
   * @param payload
   */
  changePassword(payload: ResetPassword) {
    this.resetPasswordService
      .changeCurrUserPassword(payload)
      .subscribe(data => {
        if (data.result !== 'OK') {
          this.validationMessage = this.errorTermsStoreService.getErrorMessage(
            data.error.errorCode
          );
          // this.showBanner(
          //   'positive',
          //   'positive-snackbar',
          //   'assets/images/manageplans/icon-positive.png',
          //   'PasswordResetSuccessful'
          // );
        } else {
          this.dialogRef.close({ data: data });
        }
      });
  }

  /**
   * Hides success message
   */
  hideSuccessMessage() {
    this.showSuccessMessage = false;
    this.validationMessage = '';
  }

  /**
   * Gets password rules list
   */
  getPasswordRulesList() {
    this.data.lengthRule = this.data.lengthRule;
    const mixruleSplit = this.data.mixCharRule.split('\n');
    this.mixruleSplitHeader = mixruleSplit[0];
    this.mixruleSplit = mixruleSplit.splice(1, (mixruleSplit.length - 1));
  }
}
