import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { Action } from './../models/action';
import { ResetPassword } from './../models/reset-password';
import { BaseConnectionService } from './base-connection.service';
import { PayloadCommandService } from './payload-command.service';

@Injectable({
  providedIn: 'root'
})
export class ResetPasswordService {

  constructor(private payloadCommandService: PayloadCommandService,
    private baseConnectionService: BaseConnectionService) {

  }
  changeCurrUserPassword(params?: ResetPassword, actionParam?: Action): Observable<any> {
    const moduleInfo = {
      moduleName: 'NEWPLANTAB'
    };
    const passwordPayLoad = this.payloadCommandService.getCommandDetails('changeCurrUserPassword', params, null, null, moduleInfo);
    return this.baseConnectionService.sendRequest(passwordPayLoad).pipe(map(data => data));
    // return of([]);
  }

}
