export class ListItem {
    private name: string;
    private url: string;
    private moduleName: string;
    private isEnabled: string;
    private isVisible: string;

    constructor($name: string, $url: string, $moduleName?: string,
        $isEnabled?: string, $isVisible?: string) {
        this.name = $name;
        this.url = $url;
        this.moduleName = $moduleName;
        this.isEnabled = $isEnabled;
        this.isVisible = $isVisible;
    }

    /**
     * Getter $name
     * @return string
     */
    public get $name(): string {
        return this.name;
    }

    /**
     * Setter $name
     * @param string value
     */
    public set $name(value: string) {
        this.name = value;
    }


    /**
     * Getter $url
     * @return string
     */
    public get $url(): string {
        return this.url;
    }

    /**
     * Setter $url
     * @param string value
     */
    public set $url(value: string) {
        this.url = value;
    }

    /**
    * Getter $moduleName
    * @return string
    */
    public get $moduleName(): string {
        return this.moduleName;
    }

    /**
     * Setter $moduleName
     * @param string value
     */
    public set $moduleName(value: string) {
        this.moduleName = value;
    }


    /**
     * Getter $isEnabled
     * @return string
     */
    public get $isEnabled(): string {
        return this.isEnabled;
    }

    /**
     * Setter $isEnabled
     * @param string value
     */
    public set $isEnabled(value: string) {
        this.isEnabled = value;
    }


    /**
     * Getter $isVisible
     * @return string
     */
    public get $isVisible(): string {
        return this.isVisible;
    }

    /**
     * Setter $isVisible
     * @param string value
     */
    public set $isVisible(value: string) {
        this.isVisible = value;
    }


}
