import { Observable, of, throwError } from 'rxjs';
import { tap } from 'rxjs/operators';

import { EncryptionService } from './../../services/cache/encryption.service';
import { ConfigurationService } from './../../services/configuration.service';
import { EncryptionAction } from './../../utils/cache/encryption-action.enum';
import { Store } from './store';

export class SessionStore implements Store {
  maxAge: number;
  isEncrypt: boolean;
  constructor(private encryptionService: EncryptionService,
    private config: ConfigurationService) {
    this.maxAge = config.cacheMaxAge;
  }
  // constructor() {
  //  }

  /**
   * Gets session store
   *
   * This method will check if cache key is available in cache
   * then it will check the encryption flag if it is false then
   * return the value in observable form. If it is true then decrypt the value
   * and return in observable form
   * If key not present in cache then execute the this.post method
   * after the successful exection of  this.post observable method (which return the observable)
   * set the key and value in cache
   * @param key
   * @param [callback]
   * @returns
   */
  get(key: string,
    callback?: Observable<any>) {
    if (this.hasValidCachedValue(key)) {
      if (this.isEncrypt) {
        return of(this.encryptionService.decrypt(sessionStorage.getItem(key)));
      } else {
        return of(JSON.parse(sessionStorage.getItem(key)));
      }
      //  return of(JSON.parse(sessionStorage.getItem(key)));
    }
    if (callback && callback instanceof Observable) {
      console.log(`%c Calling api for ${key}`, 'color: purple');
      return callback.pipe(tap((value) => {
        this.set(key, value);
      }));
    } else {
      return throwError('Requested key is not available in Cache');
    }
  }

  /**
   * Sets session store
   * Check the encryption flag if it true then store the value in encrypted format
   * other wise store the value in JSON.stringify format.
   * @param key
   * @param value
   */
  set(key: string, value: string) {
    // const encryptedValue = this.encryptionService.encrypt(value);
    // sessionStorage.setItem(key, JSON.stringify(value));
    // sessionStorage.setItem(key, encryptedValue);
    if (this.isEncrypt) {
      const encryptedValue = this.encryptionService.encrypt(value);
      sessionStorage.setItem(key, encryptedValue);
    } else {
      sessionStorage.setItem(key, JSON.stringify(value));
    }
  }

  /**
   * Calls session store
   * @param [fallback]
   * @param [maxAge]
   * @returns call
   */
  call(fallback?: Observable<any>, maxAge?: number): Observable<any> {
    if (fallback && fallback instanceof Observable) {
      return fallback.pipe(tap(value => value));
    }
  }

  /**
   * Removes session store
   * @param key
   */
  remove(key: string) {
    if (this.hasValidCachedValue(key)) {
      sessionStorage.removeItem(key);
    }
  }

  /**
   * Determines whether valid cached value has
   * @param key
   * @returns true if valid cached value
   */
  private hasValidCachedValue(key: string): boolean {
    let returnVal = false;
    if (sessionStorage.getItem(key) === null) {
      returnVal = false;
    } else {
      returnVal = true;

    }
    return returnVal;
  }

  /**
   * Keys exist
   * @param key
   * @returns
   */
  keyExist(key: string) {
    return of(this.hasValidCachedValue(key));
  }

  /**
   * Sets cache age
   * @param [age]
   */
  setCacheAge(age: number = 0) {
    this.maxAge = age;
  }


  /**
   * Sets encryption
   * set the encryption flag
   * @param [encrypt]
   */
  setEncryption(encrypt?: EncryptionAction) {
    this.isEncrypt = (encrypt === EncryptionAction.encrypt) ? true : false;
  }
}
