import { Injectable } from '@angular/core';
import { ServerInfo, Action } from 'common-library';
import { environment } from 'environments/environment';
import { BUILD_date, BUILD_type, BUILD_version } from 'product.constants';
import { AppBootstrapService } from 'so-common-library';

@Injectable()
export class AppService {

  constructor(private bootstrapService: AppBootstrapService) { }

  // load() {
  //   const  serverInfo: ServerInfo = {
  //     clientVersion: BUILD_version,
  //     clientBuildID: BUILD_type,
  //     clientBuildDate: BUILD_date
  //   };
  //   return this.bootstrapService.call(environment, serverInfo);
  // }

  load() {
    const  serverInfo: ServerInfo = {
      clientVersion: BUILD_version,
      clientBuildID: BUILD_type,
      clientBuildDate: BUILD_date
    };
    const actionParam: Action = {};
    actionParam.endPoint = true;
    actionParam.prelogin = false;

    return this.bootstrapService.call(environment, serverInfo, actionParam);
  }
}
