import { Injectable } from '@angular/core';

import { Payload } from '../models/payload';
import { BasePayloadCommandService } from './base-payload-command.service';
import { APIPayload } from '../models/api-payload';


@Injectable({
    providedIn: 'root'
})
export class PayloadCommandService {

    /**
     * Creates an instance of payload command service.
     * @param basePayloadCommand
     */
    constructor(private basePayloadCommand: BasePayloadCommandService) { }

    /**
     * Gets command details
     * It called the basePayloadCommand.buildRequest method which returns the xmlRequest
     * @param [command]
     * @param [params]
     * @param [tags]
     * @param [commandtags]
     * @param [moduleInfo]
     * @returns xmlRequest
     */
    getCommandDetails(command: string,
        params: any = null,
        tags: any = null,
        commandtags: any = null,
        moduleInfo: any = null): Payload {
        if (!command) {
            throw new Error(`Command name can not be empty`);
        }
        return this.basePayloadCommand.buildRequest(command,
            params,
            tags,
            commandtags,
            moduleInfo);
    }

    /**
     * Gets command details
     * It called the basePayloadCommand.buildRequest method which returns the xmlRequest
     * @param [command]
     * @param [queryParams]
     * @param [headers]
     * @param [moduleInfo]
     * @param [req]
     */
    getApiRequest(
        command: string,
        queryParams: any = null,
        headers: any = null,
        moduleInfo: any = null,
        req?: any
    ): APIPayload {
        if (!command) {
            throw new Error(`Command name can not be empty`);
        }
        return new APIPayload(command, queryParams, headers, moduleInfo, req);
    }
}
