import { Component, OnInit, ViewChild, Input, Output, EventEmitter } from '@angular/core';
import { MatMenu } from '@angular/material';
import { NestedItem } from '../../models/nested-item';

@Component({
  selector: 'ctos-nested-dropdown',
  templateUrl: './nested-dropdown.component.html',
  styleUrls: ['./nested-dropdown.component.scss']
})
export class NestedDropdownComponent implements OnInit {
  @Output() optionClick: EventEmitter<NestedItem>;
  @ViewChild('menu', { static: true }) menu: MatMenu;
  @Input() items: Array<NestedItem>;

  constructor() {
    this.optionClick = new EventEmitter();
  }

  ngOnInit() {
  }

  optionClicked(item: NestedItem) {
    this.optionClick.emit(item);
  }

}
