import { Injectable } from '@angular/core';

import { Encryption } from '../../models/cache/encryption';


@Injectable({
    providedIn: 'root'
})

export class EncryptionService implements Encryption {
    private key: string;
    private encryption: Encryption;
    private encryptionUndefinedErrorMessage: string;
    constructor() { }

    /**
     * Sets encryption
     * @param encryption
     */
    setEncryption(encryption: Encryption) {
        this.encryption = encryption;
    }

    /**
     * Sets key
     * @param key
     */
    setKey(key: string) {
        this.key = key;
    }


    /**
     * Encrypts encryption service
     * @param obj
     * @param [key]
     * @returns encrypt
     */
    encrypt(obj: Object, key: string = this.key): string {
        let ret = null;
        if (this.encryption !== undefined && key !== undefined) {
            ret = this.encryption.encrypt(obj, key);
        } else {
            throw new Error('Encryption object or key not set');
        }
        return ret;
    }


    /**
     * Decrypts encryption service
     * @param text
     * @param [key]
     * @returns decrypt
     */
    decrypt(text: Object, key: string = this.key): Object {
        let ret = null;
        if (this.encryption !== undefined && key !== undefined) {
            ret = this.encryption.decrypt(text, key);
        } else {
            throw new Error('Encryption object or key not set');
        }
        return ret;
    }
}
