import { ListItem } from './list-item';

export class UserProfile {
    private name: string;
    private items: ListItem[];
    private nameSeparator: string;

    constructor($name: string, $items: ListItem[], $nameSeparator: string) {
        this.name = $name;
        this.items = $items;
        this.nameSeparator = $nameSeparator;
    }

    /**
     * Getter $name
     * @return string
     */
    public get $name(): string {
        return this.name;
    }

    /**
     * Setter $name
     * @param string value
     */
    public set $name(value: string) {
        this.name = value;
    }

    /**
     * Getter $items
     * @return ListItem[]
     */
    public get $items(): ListItem[] {
        return this.items;
    }

    /**
     * Setter $items
     * @param ListItem[] value
     */
    public set $items(value: ListItem[]) {
        this.items = value;
    }

    /**
     * Getter $nameSeparator
     * @return string
     */
    public get $nameSeparator(): string {
        return this.nameSeparator;
    }

    /**
     * Setter $nameSeparator
     * @param string value
     */
    public set $nameSeparator(value: string) {
        this.nameSeparator = value;
    }
}
