import { Injectable } from '@angular/core';

import { CUSTOM_ERROR_CODES } from '../utils/service-constant';
import { StringUtil } from '../utils/string-util';
import { AppUtil } from '../utils/AppUtil';


@Injectable({
    providedIn: 'root'
})
export class ErrorTermsStoreService {

    /**
     * Creates an instance of error terms store service.
     */
    constructor() { }

    errorTerminologies = null;
    private errorCodeBase = {};


    /**
     * Creates error code base
     * @param errorTerminologies
     */
    createErrorCodeBase(errorTerminologies: any) {
        const that = this;
        that.errorTerminologies = errorTerminologies;
        const iLen = that.errorTerminologies.term.length;
        for (let i = 0; i < iLen; i++) {
            that.errorCodeBase[that.errorTerminologies.term[i].name] = that.errorTerminologies.term[i].value;
        }
    }

    /**
     * Gets error message
     * @param name
     * @returns
     */
    getErrorMessage(name) {
        const that = this;
        if (!isNaN(name)) {
            return that.errorCodeBase['E' + name];
        } else {
            return that.errorCodeBase[name];
        }
    }

    /**
   * Method to extract the error messages from error store and concat all messages into one
   * @param errors
   * @returns consolidated error message
   * @memberof ErrorTermsStoreService
   */
    consolidateValidationErrors(errors: any, fsiDate?: any, fssDate?: any): string {
        let errorMessage = '';
        if (errors) {
            // tslint:disable-next-line:prefer-const
            let errorMessages = [];
            Object.keys(errors).forEach(errorCode => {
                // tslint:disable-next-line:no-shadowed-variable
                let errorMessage = this.getErrorMessage(
                    errorCode
                );
                if (!errorMessage) {
                    if (errorCode === CUSTOM_ERROR_CODES.MAX.error) {
                        errorMessage =
                            this.getErrorMessage(CUSTOM_ERROR_CODES.MAX.message) +
                            ' ' +
                            errors.max.max + '.';
                    } else if (errorCode === CUSTOM_ERROR_CODES.MIN.error) {
                        errorMessage =
                            this.getErrorMessage(CUSTOM_ERROR_CODES.MIN.message) +
                            ' ' +
                            errors.min.min + '.';
                    } else if (errorCode === CUSTOM_ERROR_CODES.MAT_DATEPICKER_PARSE.error) {
                        errorMessage = this.getErrorMessage(
                            CUSTOM_ERROR_CODES.MAT_DATEPICKER_PARSE.message
                        );
                    } else if (errorCode === CUSTOM_ERROR_CODES.DAYS_TO_SI_AFTER_ARTB.error) {
                        errorMessage = this.getErrorMessage(
                            CUSTOM_ERROR_CODES.DAYS_TO_SI_AFTER_ARTB.message
                        );
                    } else if (errorCode === CUSTOM_ERROR_CODES.FSI_REQUIRED_FOR_FSS.error) {
                        errorMessage = this.getErrorMessage(CUSTOM_ERROR_CODES.FSI_REQUIRED_FOR_FSS.message);
                    } else if (errorCode === CUSTOM_ERROR_CODES.REQUIRED.error) {
                        errorMessage = this.getErrorMessage(CUSTOM_ERROR_CODES.REQUIRED.message);
                    } else if (errorCode === CUSTOM_ERROR_CODES.ARTB_LT_EQ_FSI.error) {
                        errorMessage = StringUtil.interpolate(
                            this.getErrorMessage(CUSTOM_ERROR_CODES.ARTB_LT_EQ_FSI.message),
                            [AppUtil.formatDateStrict(errors.ARTB_LT_EQ_FSI.ARTB_LT_EQ_FSI)]);
                    } else if (errorCode === CUSTOM_ERROR_CODES.ARTB__LT_EQ_FSS.error) {
                        errorMessage = StringUtil.interpolate(
                            this.getErrorMessage(CUSTOM_ERROR_CODES.ARTB__LT_EQ_FSS.message),
                            [AppUtil.formatDateStrict(errors.ARTB__LT_EQ_FSS.ARTB__LT_EQ_FSS)]);
                    } else if (errorCode === CUSTOM_ERROR_CODES.FSI_GT_TLSS.error) {
                        errorMessage = StringUtil.interpolate(
                            this.getErrorMessage(CUSTOM_ERROR_CODES.FSI_GT_TLSS.message),
                            [AppUtil.formatDateStrict(errors.FSI_GT_TLSS.FSI_GT_TLSS)]);
                    } else if (errorCode === CUSTOM_ERROR_CODES.FSILT_ERROR.error) {
                        errorMessage = StringUtil.interpolate(
                            this.getErrorMessage(CUSTOM_ERROR_CODES.FSILT_ERROR.message),
                            [AppUtil.formatDateStrict(errors.FSILT_ERROR.FSILT_ERROR)]);
                    } else if (errorCode === CUSTOM_ERROR_CODES.DEPENDENCY_ERROR.error) {
                        errorMessage = StringUtil.interpolate(
                            this.getErrorMessage(CUSTOM_ERROR_CODES.DEPENDENCY_ERROR.message),
                            [AppUtil.formatDateStrict(errors.DEPENDENCY_ERROR.DEPENDENCY_ERROR)]);
                    }
                }
                errorMessages.push(errorMessage);
            });
            errorMessage = errorMessages.join('\n');
        }
        return errorMessage;
    }
}
