import { EncryptionService } from './../../services/cache/encryption.service';
import { InMemoryService } from './../../services/cache/in-memory.service';
import { ConfigurationService } from './../../services/configuration.service';
import { StoreType } from './../../utils/cache/store-type.enum';
import { Factory } from './factory';
import { SessionStore } from './session-store';


/**
 * Cache factory
 */
export class CacheFactory implements Factory {
    createStore(storeType: StoreType, encryptionService?: EncryptionService, config?: ConfigurationService,
        inMemory?: InMemoryService) {
        switch (storeType) {
            case StoreType.InMemory:
                return inMemory;
            case StoreType.Session:
                return new SessionStore(encryptionService, config);
            default:
                return null;
        }
    }
}
