
/**
 * String util
 */
export class StringUtil {

    /**
     * Escapes html
     * @param text
     * @returns html
     */
    static escapeHTML(text: string): string {
        if (undefined === text || null === text) {
            throw new Error('text should be defined');
        }
        if (typeof text !== 'string') {
            throw new Error('text should be a string');
        }
        const replacements = {
            '<': '&lt;',
            '>': '&gt;',
            '&': '&amp;',
            '\"': '&quot;',
            '\'': '&apos;'
        };
        const result = text.replace(/[<>&"']/g, function (character) {
            return replacements[character];
        });
        return result;
    }

    /**
     * Interpolates arguments into string
     * for e.g. if str = test{0} and args=['abc']
     * the method will return string 'testabc'
     * @param str
     * @param args
     * @returns interpolate
     */
    static interpolate(str: string, args: string[]): string {
        if (undefined === str || null === str) {
            throw new Error('str should be defined');
        }
        if (typeof str !== 'string') {
            throw new Error('str should be a string');
        }
        if (undefined === args || null === args) {
            throw new Error('args should be defined');
        }
        if (!(args instanceof Array)) {
            throw new Error('args should be an array');
        }
        if (args.length === 0) {
            return str;
        }
        const result = str.replace(/(\{([0-9])\})/g, function (match, p1, idx) {
            return args[idx];
        });
        return result;
    }

    /**
     * Converts a string into CamelCase
     *
     * @param s
     * @returns string
     * @memberof AppUtil
     */
    static camelize(s: string): string {
        if (undefined === s || null === s) {
            throw new Error('s should be defined');
        }
        let ret = '';
        s = s.toLowerCase();
        let arr = s.split(' ');
        arr = arr.map((a) => {
            const regex = /(?:^|[-_])(\w)/g;
            return a.replace(regex, function (stripped, letter) {
                return letter ? letter.toUpperCase() : '';
            });
        });
        ret = arr.join(' ');
        return ret;
    }

    static substitute(str: string, args: string[]): string {
        const result = str.replace(/(\{([0-9])\})/g, function (match, p1, idx) {
            return args[idx];
        });
        return result;
    }
}


