import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewEncapsulation,
  OnDestroy,
  OnChanges,
  SimpleChanges
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { TabItem } from '../../models/tab-item';

@Component({
  selector: 'ctos-tab',
  templateUrl: './tab.component.html',
  encapsulation: ViewEncapsulation.None
})
export class TabComponent implements OnInit, OnDestroy, OnChanges {
  showTooltip: boolean;
  @Input() isCohortPlan: string;
  @Input() items: Array<TabItem>;
  @Output() tabClicked: EventEmitter<TabItem>;
  @Input() selectedTabItem: TabItem;

  selectedTab: TabItem;

  /**
   * Creates an instance of tab component.
   * @param activatedRoute
   */
  constructor(private activatedRoute: ActivatedRoute) {
    this.tabClicked = new EventEmitter<TabItem>();
  }

  ngOnInit() {
    const currentRoutePath = this.activatedRoute.snapshot.firstChild.url[0]
      .path;
    if (this.items) {
      this.selectedTab = this.items.find(i => {
        return i.routePath === currentRoutePath;
      });
    }
  }

  /**
   * Selects tab
   * @param item
   * @returns
   */
  selectTab(item: TabItem) {
    if (!item.enable) {
      return;
    }
    this.selectedTab = item;
    this.tabClicked.emit(item);
  }
  /**
   * Gets mouse event
   * @param tabName
   * @param enability
   */
  getMouseEvent(tabName, enability) {
    this.showTooltip = false;
    if (tabName === 'Countries' && !enability && this.isCohortPlan === 'Y') {
      this.showTooltip = true;
    }
  }

  /**
   * on destroy
   */
  ngOnDestroy(): void {
    this.selectedTab = null;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.items && changes.items.currentValue) {
      this.items = changes.items.currentValue;
    }
    if (changes.selectedTabItem && changes.selectedTabItem.currentValue) {
      this.selectedTab = changes.selectedTabItem.currentValue;
    }
  }
}
