  getStudyRefreshDate(): Observable<any> {
    const self = this;
    const studyKey = self.userDataService.getLastVisitedStudyKey();
    return self.getStudySummaryDetails(studyKey).pipe(
      map(resp => {
        return self.studyRefreshDateMap(resp);
      })
    );
  }

  getUpdatedMRUStudyList(studyList: string, selectedStudy: any): string {
    // tslint:disable-next-line:prefer-const
    let studyListArr = studyList.split(';');
    const index = studyListArr.indexOf(selectedStudy.key.toString());
    if (index > -1) {
      studyListArr.splice(index, 1);
    }
    if (studyListArr.length > 4) {
      studyListArr.shift();
    }
    studyListArr.push(selectedStudy.key);
    return studyListArr.join(';');
  }

  /**
  * Gets study summary details
  * @param studyKey
  * @param [baseParam]
  * @returns
  */
  getStudySummaryDetails(studyKey, baseParam?: Action) {
    const studyParam = {
      commandName: COMMAND.STUDYSUMMARY,
      param: {
        studyKey: studyKey
      }
    };
    const that = this;
    const moduleInfo = CTOSUtil.getModuleInfo('PLANCARD', studyKey);
    // const planListPayLoad = that.payloadCommandService.getCommandDetails(
    //   studyParam.commandName,
    //   studyParam.param,
    //   null,
    //   null,
    //   moduleInfo
    // );
    const payload = this.payloadCommandService.getApiRequest(
      studyParam.commandName,
      studyParam.param,
      null,
      moduleInfo
    );


    const actionParam: Action = {};
    if (!baseParam) {
      baseParam = actionParam;
    }
    // baseParam.cacheKey =
    //   CacheKey.GetStudySummary + '_' + studyParam.param.studyKey;
    baseParam.url = APIEndPoints.STUDY_SUMMARY;
    // return that.baseConnectionService
    //   .sendRequest(planListPayLoad, baseParam)
    //   .pipe(
    //     map(resp => {
    //       this._studySummary.next(resp);
    //       return resp.studySummary;
    //     })
    //   );
    return this.baseConnectionService
      .send(payload, baseParam)
      .pipe(
        map(response => {
          this._studySummary.next(response);
          return response.studySummary;
        })
      );
  }

  private studyRefreshDateMap(response): string {
    let studyRefreshDate = '';
    if (response && response.studyThumbnail && AppUtil.isDefined(response.studyThumbnail.studyRefreshDate, false)) {
      studyRefreshDate = response.studyThumbnail.studyRefreshDate;
    }
    return studyRefreshDate;
  }


