import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import {
  DateAdapter,
  MAT_DATE_FORMATS,
  MAT_DATE_LOCALE,
  MatButtonModule,
  MatButtonToggleModule,
  MatCheckboxModule,
  MatDatepickerModule,
  MatRadioModule,
  MatSelectModule,
  MatProgressSpinnerModule,
  MAT_PROGRESS_SPINNER_DEFAULT_OPTIONS,
  MAT_SELECT_SCROLL_STRATEGY,
  MatIconModule,
  MatTreeModule,
  MatNativeDateModule,
  MatTableModule,
  MatDialogModule,
  MatTabsModule,
  MAT_DIALOG_DEFAULT_OPTIONS,
  MatPaginatorModule
} from '@angular/material';
import { MatMomentDateModule } from '@angular/material-moment-adapter';
import { MatAutocompleteModule, MAT_AUTOCOMPLETE_SCROLL_STRATEGY } from '@angular/material/autocomplete';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { MatMenuModule, MAT_MENU_SCROLL_STRATEGY } from '@angular/material/menu';
import {
  MAT_SNACK_BAR_DEFAULT_OPTIONS,
  MatSnackBarModule
} from '@angular/material/snack-bar';
import { MatTooltipModule } from '@angular/material/tooltip';

import { CTOS_DATE_FORMATS, CTOSDateAdapter } from './ctos-date-adapter';
import { OverlayModule, Overlay } from '@angular/cdk/overlay';
import { MAT_SCROLL_STRATEGY_FACTORY } from './scroll';
import { MatSlideToggleModule } from '@angular/material/slide-toggle';

@NgModule({
  imports: [
    CommonModule,
    MatAutocompleteModule,
    FormsModule,
    ReactiveFormsModule,
    MatSnackBarModule,
    MatIconModule,
    MatTreeModule,
    MatNativeDateModule,
    MatTableModule,
    MatTooltipModule,
    MatDialogModule,
    MatMenuModule,
    MatSelectModule,
    MatInputModule,
    MatFormFieldModule,
    MatTabsModule,
    MatCheckboxModule,
    MatRadioModule,
    MatDatepickerModule,
    MatMomentDateModule,
    MatButtonModule,
    MatButtonToggleModule,
    OverlayModule,
    MatProgressSpinnerModule,
    MatPaginatorModule,
    MatSlideToggleModule
  ],
  providers: [
    {
      provide: MAT_SNACK_BAR_DEFAULT_OPTIONS,
      useValue: { duration: 2000, verticalPosition: 'top' }
    },
    {
      provide: DateAdapter,
      useClass: CTOSDateAdapter,
      deps: [MAT_DATE_LOCALE]
    },
    { provide: MAT_DATE_FORMATS, useValue: CTOS_DATE_FORMATS },
    {
      provide: MAT_PROGRESS_SPINNER_DEFAULT_OPTIONS,
      useValue: { _forceAnimations: true, diameter: 100, strokeWidth: 6 }
    },
    {
      provide: MAT_SELECT_SCROLL_STRATEGY,
      useFactory: MAT_SCROLL_STRATEGY_FACTORY,
      deps: [Overlay]
    },
    {
      provide: MAT_MENU_SCROLL_STRATEGY,
      useFactory: MAT_SCROLL_STRATEGY_FACTORY,
      deps: [Overlay]
    },
    {
      provide: MAT_AUTOCOMPLETE_SCROLL_STRATEGY,
      useFactory: MAT_SCROLL_STRATEGY_FACTORY,
      deps: [Overlay]
    },
    {
      provide: MAT_DIALOG_DEFAULT_OPTIONS,
      useValue: {
        hasBackdrop: true,
        disableClose: true,
        backdropClass: 'backdrop'
      }
    }
  ],
  declarations: [],
  exports: [
    MatAutocompleteModule,
    FormsModule,
    ReactiveFormsModule,
    MatSnackBarModule,
    MatIconModule,
    MatTreeModule,
    MatNativeDateModule,
    MatTableModule,
    MatTooltipModule,
    MatDialogModule,
    MatMenuModule,
    MatSelectModule,
    MatInputModule,
    MatFormFieldModule,
    MatTabsModule,
    MatCheckboxModule,
    MatRadioModule,
    MatDatepickerModule,
    MatMomentDateModule,
    MatButtonModule,
    MatButtonToggleModule,
    OverlayModule,
    MatProgressSpinnerModule,
    MatPaginatorModule,
    MatSlideToggleModule
  ]
})
export class CommonAngularMaterialModule { }
