
/**
 * Payload
 */
export class Payload {
    public command: string;
    public params: {};
    public xmlRequest: string;

/**
 * Creates an instance of payload.
 * @param command
 * @param params
 * @param xmlRequest
 */
constructor(command: string, params: {}, xmlRequest: string) {
        this.command = command;
        this.params = params;
        this.xmlRequest = xmlRequest;
    }
}
