import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';

import { ListItem } from './../../models/list-item';
import { SearchListInput } from './../../models/search-list-input';
import { FormGroup } from '@angular/forms';
import { Observable, of, Subscription } from 'rxjs';
import { DialogService } from '../../services/dialog.service';
import { Router } from '@angular/router';

@Component({
  selector: 'ctos-sub-header',
  templateUrl: './sub-header.component.html'
})
export class SubHeaderComponent implements OnInit, OnDestroy {
  isSearchListOn = true;
  @Input()
  subHeaders: ListItem[];
  @Input()
  studyRefreshDate: string;
  @Input()
  studyRefreshDateLabel: string;
  @Input() studyListInput: SearchListInput;
  @Output()
  itemSelect: EventEmitter<any>;
  @Output() headerTabClicked: EventEmitter<string>;
  searchListFormGroup: FormGroup;
  disableLink = false;
  @Input() disablePlansTab: boolean;
  @Input()
  set searchListFormGroupName(value: FormGroup) {
    this.searchListFormGroup = value;
  }
  get searchListFormGroupName(): FormGroup {
    return this.searchListFormGroup;
  }

  /**
   * Creates an instance of sub header component.
   * @param dialogService
   */
  constructor(private dialogService: DialogService,
    private router: Router) {
    this.itemSelect = new EventEmitter();
    this.headerTabClicked = new EventEmitter();
  }

  ngOnInit() { }

  /**
   * Determines whether tab click on
   * @param moduleName
   * @param $event
   */
  onTabClick(moduleName: string, $event) {
    const that = this;
    that.canDeactivate().subscribe(res => {
      if (that.disableLink === true && moduleName === 'MODULE_PLANNING') {
        $event.stopPropagation();
        $event.preventDefault();
      } else if (res === true) {
        this.headerTabClicked.emit(moduleName);
      }
    });
  }

  /**
   * Items selected
   * @param event
   */
  itemSelected(event) {
    this.itemSelect.emit(event);
  }

  /**
   * Determines whether study change on
   * @param studyTag
   */
  onStudyChange(studyTag) {
    if (this.disablePlansTab === true && studyTag && studyTag.newPlanInd === true) {
      this.disableLink = true;
    } else {
      this.disableLink = false;
    }
  }

  /**
   * Determines whether deactivate can
   * @param [isReset]
   * @returns deactivate
   */
  canDeactivate(isReset?: boolean): Observable<boolean> {
    if (isReset === true) {
      return of(true);
    }
    if (this.dialogService.isStatusChanged === true) {
      return this.dialogService.confirm();
    }
    return of(true);
  }

  isTabActive(item: ListItem): boolean {
    let tabActive = false;
    const url = this.router.url.split('/');
    if (url.length > 1) {
      const currentUrl = url[1];
      tabActive = item.$name.toLowerCase() === currentUrl.toLowerCase();
    }
    return tabActive;
  }

  ngOnDestroy() {
    this.subHeaders = null;
    this.studyListInput = null;
    this.itemSelect = null;
  }

}
