import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { map } from 'rxjs/operators';

import { AppWindow } from '../models/appWindow';
import { DEFAULT_HELP_ID, HOSTED_HELP_COMMAND, HOSTED_HELP_URL } from '../utils/help-constants';
import { COMMON } from '../utils/service-constant';
import { BaseConnectionService } from './base-connection.service';
import { PayloadCommandService } from './payload-command.service';

@Injectable({
    providedIn: 'root'
})
export class HelpService {
    constructor(private route: ActivatedRoute,
        private payloadCommandService: PayloadCommandService,
        private baseConnectionService: BaseConnectionService) {
    }

    openHelp() {
        const windowObj = AppWindow.getWindow();
        // let url =
        //     windowObj.location.protocol +
        //     '//' +
        //     windowObj.location.hostname +
        //     ':' +
        //     windowObj.location.port +
        //     '/';
        // if (BASE_HREF) {
        //     url += BASE_HREF + '/';
        // }
        // url += HELP_INDEX_PAGE_PATH;
        let activeRoute = this.route;
        if (activeRoute) {
            while (activeRoute.firstChild) {
                activeRoute = activeRoute.firstChild;
            }
            const activeRouteHelpId = (<any>activeRoute.snapshot && <any>activeRoute.snapshot.data
                && (<any>activeRoute.snapshot.data).helpId) ? (<any>activeRoute.snapshot.data).helpId : DEFAULT_HELP_ID;
            // url += HELP_MAP[activeRouteHelpId];
            // windowObj.open(url, '_blank', 'width=1100,height=500');

            const params = {
                pagecontext: activeRouteHelpId
            };
            const moduleInfo = {
                moduleName: 'NEWPLANTAB'
            };
            const hostedHelpPayload = this.payloadCommandService.getCommandDetails(HOSTED_HELP_COMMAND, params, null, null, moduleInfo);
            this.baseConnectionService.sendRequest(hostedHelpPayload, { url: HOSTED_HELP_URL }).pipe(
                map(resp => {
                    if (resp.result === COMMON.RESULT_OK) {
                        return resp.helpUrl;
                    } else {
                        return null;
                    }
                })
            ).subscribe((helpUrl) => {
                if (helpUrl) {
                    windowObj.open(helpUrl, '_blank', 'width=1100,height=500');
                }
            });
        }
    }
}
