import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CustomPreloadStrategy } from './custom-preload-strategy';

const routes: Routes = [];


import { environment } from 'environments/environment';
import { AuthGuard } from 'so-common-library';


const appRoutes: Routes = [
  {
    path: 'sites',
    // loadChildren: '../app/study/study.module#StudyModule',
    loadChildren: () => import('../app/sites/sites.module').then(m => m.SitesModule),
    canActivate: [AuthGuard],
    data: { 'loginUrl': environment.login_url, 'moduleId': 5 }
  },
  {
    path: '', redirectTo: 'sites', pathMatch: 'full', canActivate: [AuthGuard],
    data: { 'loginUrl': environment.login_url, 'moduleId': 5 }
  }
];

@NgModule({
  // imports: [RouterModule.forRoot(routes)],
  imports: [RouterModule.forRoot(appRoutes, { paramsInheritanceStrategy: 'always', preloadingStrategy: CustomPreloadStrategy })],
  exports: [RouterModule],
  providers: [CustomPreloadStrategy]
})
export class AppRoutingModule { }
