import { formatNumber } from '@angular/common';
import { AbstractControl, FormGroup } from '@angular/forms';
import { UrlSegment } from '@angular/router';
import * as momentNS from 'moment';

const moment = momentNS;

/**
 * App util
 */
export class AppUtil {
  /**
   * Checks if the element exists in the given array
   *
   * @param arr
   * @param element
   * @returns
   * @memberof AppUtil
   */
  static checkElementExistsInArray(arr: any[], element) {
    if (arr === undefined || arr === null) {
      throw new Error('arr should be defined');
    }
    if (!(arr instanceof Array)) {
      throw new Error('arr should be an array');
    }
    if (element === undefined || element === null) {
      throw new Error('element should be defined');
    }
    const result = arr.some(function (el) {
      return el.key === element.key;
    });
    return result;
  }

  /**
   * extracts and concatenates the path from URL segments
   *
   * @param url
   * @returns
   * @memberof AppUtil
   */

  static extractPathFromUrl(url: UrlSegment[]) {
    if (url === undefined || url === null) {
      throw new Error('url must be defined');
    }
    if (!(url instanceof Array)) {
      throw new Error('url must be of type UrlSegment');
    }
    const paths = url.map(segment => segment.path);
    const path = paths.join('/');
    return path;
  }

  static getInitials(text: string, separator: string): string {
    if (text === undefined || text === null) {
      throw new Error('text should be defined');
    }
    if (typeof text !== 'string') {
      throw new Error('text should be string');
    }
    const arr = text.split(separator);
    const initials = arr.map(t => {
      return t
        .trim()
        .charAt(0)
        .toUpperCase();
    });
    return initials.join('');
  }

  static getNameInitials(name: string, separator: string): string {
    if (name === undefined || name === null) {
      throw new Error('name should be defined');
    }

    if (typeof name !== 'string') {
      throw new Error('name should be string');
    }

    const arr = name.split(separator);
    const initials = [];
    if (arr.length > 0) {
      initials.push(
        arr[0]
          .trim()
          .charAt(0)
          .toUpperCase()
      );
    }
    if (arr.length > 1) {
      initials.push(
        arr[arr.length - 1]
          .trim()
          .charAt(0)
          .toUpperCase()
      );
    }
    return initials.join('');
  }

  static generateUrl(baseHref: string, fragment: string): string {
    let url =
      window.location.protocol +
      '//' +
      window.location.hostname +
      ':' +
      window.location.port +
      '/';

    if (baseHref) {
      url += baseHref + '/';
    }
    if (fragment) {
      url += fragment + '/';
    }
    url += 'index.html';

    return url;
  }

  static generateFlexUrl(baseHref: string): string {
    let url = '';
    url +=
      window.location.protocol +
      '//' +
      window.location.hostname +
      ':' +
      window.location.port +
      '/';
    if (baseHref) {
      url += baseHref + '/';
    }
    return url;
  }

  static dayDiff(fromdt: any, todt: any): number {
    const fromDate = moment(fromdt);
    const toDate = moment(todt);
    const daysDifference = toDate.diff(fromDate, 'days');
    return daysDifference;
    // const weekDiffernce = daysDifference / 7;
    // return weekDiffernce.toFixed(1) + ` ` + `Weeks`;
  }

  static dateDiff(dt1: any, dt2: any, by?: string, num?: number) {
    const fromDate = moment(dt1);
    const toDate = moment(dt2);
    const dayDiff = toDate.diff(fromDate, 'd');
    if (by === 'd') {
      return dayDiff;
    } else {
      const weekDiff = (dayDiff + 1) / 7;
      return weekDiff.toFixed(num);
    }
  }
  static dateAdd(date: any, num: number) {
    return moment(date).add(num, 'days');
  }
  static dateSubstract(date: any, num: number) {
    return moment(date).subtract(num, 'days');
  }
  static dateFormatter(date?: any) {
    return moment(date).format('YYYY-MM-DD');
  }

  static formatDate(date?: any, format: string = 'YYYY-MM-DD') {
    return moment(date).format(format);
  }

  static formatDateStrict(date?: any, format: string = 'DD-MMM-YYYY', emptyValue: string = ''): string {
    if (this.isDefined(date)) {
      return moment(date).format(format);
    } else {
      return emptyValue;
    }
  }

  static dateCompare(dt1: any, dt2: any) {
    const date1 = dt1;
    const date2 = dt2;
    const days = moment(date2).diff(moment(date1), 'days');
    if (days >= 0) {
      return true;
    } else {
      return false;
    }
  }

  /********************************************************************
  * Function : isValidDate
  * Purpose: Utility method to validate date formate & the value.
  * Valid formate: DD-MMM-YYYY (For e.g. 05-Dec-2016). If user enters something which is not in this format
  * then it will return false. For e.g. 05..Dec.2016 will return false
  *********************************************************************/
  static isValidDate(dateStr: any) {
    const currVal = dateStr;
    const months = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC'];
    if (currVal === '' && currVal) {
      return false;
    }
    const dtPattern = /^(([1-9]|0[1-9]|1[0-9]|2[0-9]|3[0-1])(-)(JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC)(-)((19|20)\d{2}))$/i;
    const dtArray = currVal.match(dtPattern);
    if (dtArray === null || dtArray === undefined) {
      return false;
    }
    const dtDay = parseInt(dtArray[1], 10);
    let dtMonth = months.indexOf(dtArray[4].toUpperCase()) + 1;
    const dtYear = parseInt(dtArray[6], 10);
    if (isNaN(dtMonth)) {
      for (let i = 4; i <= 15; i++) {
        if ((dtArray[i])) {
          dtMonth = i - 3;
          break;
        }
      }
    }
    if (dtMonth < 1 || dtMonth > 12) {
      return false;
    } else if (dtDay < 1 || dtDay > 31) {
      return false;
    } else if ((dtMonth === 4 || dtMonth === 6 || dtMonth === 9 || dtMonth === 11) && dtDay === 31) {
      return false;
    } else if (dtMonth === 2) {
      const isleap = (dtYear % 4 === 0 && (dtYear % 100 !== 0 || dtYear % 400 === 0));
      if (dtDay > 29 || (dtDay === 29 && !isleap)) {
        return false;
      }
    }
    return true;
  }

  static isDefined(value: string | number, isNumber: boolean = false): boolean {
    let isDefined = false;
    if (value !== undefined && value !== null) {
      isDefined = true;
      if (isNumber) {
        value = parseFloat(value.toString().split(',').join(''));
        isDefined = (isDefined && !isNaN(<number>value) && isFinite(<number>value));
      } else {
        isDefined = (isDefined && value.toString().trim() !== '');
      }
    }
    return isDefined;
  }

  static formatDecimal(value: number, format?: string): number {
    const formattedValue = formatNumber(value, 'en-US', format);
    value = Number(formattedValue.split(',').join(''));
    if (isNaN(value)) {
      value = null;
    }
    return value;
  }

  static stripNonnumbers(value: string): string {
    const regEx = /[^0-9]/g;
    return value ? value.replace(regEx, '') : value;
  }

  static isValidNumber(inputNumber: any): boolean {
    if (inputNumber && (typeof inputNumber === 'string')) {
      inputNumber = inputNumber.toString().trim();
    }
    if (inputNumber || inputNumber === 0) {
      return !isNaN(inputNumber);
    }

    return false;
  }

  /**
   * Checks if the given arrays are equal. If yes returns true and false otherwise.
   *
   * @param a
   * @param b
   * @memberof AppUtil
   */
  static areArraysEqual(a: Array<any>, b: Array<any>): boolean {
    let arraysEqual = false;
    if (a && b) {
      arraysEqual = [...a].sort().join(',') === [...b].sort().join(',');
    }
    return arraysEqual;
  }

  /**
   * Clears the validators applied on the formgroup as well as all its descendants
   * and updates their validity
   *
   * @param formGroup
   * @memberof AppUtil
   */
  static clearFormGroupValidators(formGroup: FormGroup) {
    if (formGroup) {
      for (const field in formGroup.controls) {
        if (formGroup.get(field)) {
          const fg = formGroup.get(field);
          fg.clearValidators();
          fg.updateValueAndValidity();
          if ((<FormGroup>fg).controls) {
            this.clearFormGroupValidators(<FormGroup>fg);
          }
        }
      }
      formGroup.clearValidators();
      formGroup.updateValueAndValidity();
    }
  }

  /**
   * Resets the error if any set on the form group as well as all its descendants
   *
   * @param form
   * @memberof AppUtil
   */
  static resetFormErrors(form: AbstractControl) {
    if (form) {
      form.setErrors(null);
      const fg = <FormGroup>form;
      if (fg && fg.controls && Object.keys(fg.controls).length > 0) {
        for (const field in fg.controls) {
          if (fg.get(field)) {
            const fgEntry = fg.get(field);
            this.resetFormErrors(fgEntry);
          }
        }
      }
    }
  }

  /**
    * Calcuaters rate per frequency
    * @param rate
    * @param reportingPeriodVal
    * @returns rate per frequency
    */
  static calcuaterRatePerFrequency(rate: number, reportingPeriodVal: number, isModify?: boolean): number {
    const ZERO = 0;
    if (this.isDefined(rate)) {
      if (rate !== ZERO) {
        const freq = reportingPeriodVal;
        // if (isModify && isModify === true) {
        //   return rate * freq;
        // } else {
        //   return (rate / freq);
        // }
        return rate;
      } else {
        return ZERO;
      }
    } else {
      return ZERO;
    }
  }

  /**
    * Checks not empty
    * @param [param]
    * @returns true if not empty
    */
  static checkNotEmpty(param?: any): boolean {
    const EMPTY = '';
    if (param !== null && param !== undefined) {
      if (param !== EMPTY) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  /**
* Dates formatters
* @param [date]
* @returns
*/
  static dateFormatters(date?: any) {
    const EMPTY = '';
    if (this.isDefined(date)) {
      return moment(date).format('YYYY-MM-DD');
    } return EMPTY;
  }


}
