import { Component, OnInit, Input, Inject } from '@angular/core';
import { MatSnackBar, MatSnackBarRef } from '@angular/material/snack-bar';
import { MAT_SNACK_BAR_DATA } from '@angular/material';


@Component({
  selector: 'ctos-display-banner',
  templateUrl: './display-banner.component.html'
})
export class DisplayBannerComponent implements OnInit {
  /**
   * Creates an instance of display banner component.
   * @param data
   * @param snackBarRef
   */
  constructor(@Inject(MAT_SNACK_BAR_DATA) public data: any,
    private snackBarRef: MatSnackBarRef<DisplayBannerComponent>) {
  }
  ngOnInit() {
  }
  /**
   * Closes display banner component
   */
  close() {
    this.snackBarRef.dismiss();
  }
}
