import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';


@Component({
  selector: 'ctos-auth-main-header',
  templateUrl: './auth-main-header.component.html',
  encapsulation: ViewEncapsulation.None
})
export class AuthMainHeaderComponent implements OnInit {

  @Input() logoUrl: string;
  @Input() appInfoUrl: string;

  hideCustomerLogo = false;

  constructor(
  ) {
  }

  ngOnInit() {
  }

  /**
   * Hides image
   * @param event
   */
  hideImage(event) {
    this.hideCustomerLogo = true;
  }
}
