import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { MatDialog, MatSnackBar, MatMenu } from '@angular/material';
import { Router } from '@angular/router';
import { Subscription, Observable, of } from 'rxjs';

import { PasswordResource } from '../../models/password-resource';
import { HelpService } from '../../services/help.service';
import { ConfirmationDialogComponent } from '../confirmation-dialog/confirmation-dialog.component';
import { DisplayBannerComponent } from '../display-banner/display-banner.component';
import { AboutResource } from './../../models/about-resource';
import { AppWindow } from './../../models/appWindow';
import { ListItem } from './../../models/list-item';
import { UserProfile } from './../../models/user-profile';
import { ConfigurationService } from './../../services/configuration.service';
import { ErrorTermsStoreService } from './../../services/error-terms-store.service';
import { LocaleAppResourcesService } from './../../services/locale-app-resources.service';
import { LogoutService } from './../../services/logout.service';
import { AppUtil } from './../../utils/AppUtil';
import { COMMON, DIALOG_CONFIG } from './../../utils/service-constant';
import { PasswordComponent } from './../password/password.component';
import { DialogService } from '../../services/dialog.service';
import { ServerStatusService } from '../../services/server-status.service';
import { HeaderClickAction } from './header-click.action';
import { Action } from '../../models/action';

@Component({
  selector: 'ctos-main-header',
  templateUrl: './main-header.component.html',
  encapsulation: ViewEncapsulation.None
})
export class MainHeaderComponent implements OnInit, OnChanges, OnDestroy, AfterViewInit {
  @Input() items: ListItem[];
  @Input() userProfile: UserProfile;
  @Input() logoUrl: string;
  @Input() appInfoUrl: string;
  @Input() alertConfirmUrl: string;
  @Input() aboutResource: AboutResource;
  @Input() passwordResource: PasswordResource;
  @Output() headerTabClicked: EventEmitter<string>;
  contextMenuClass = 'main-header-context-menu';

  toShowOverlay: Boolean = false;
  passwordComponent: PasswordComponent;
  hideCustomerLogo = false;

  userInitials: string;
  show: boolean;
  firstName: string;
  lastName: string;
  UserNameToolTip: string;
  profileItems: ListItem[];
  LogoutMessage1: string;
  LogoutMessage2: string;
  OkLabel: string;
  CancelLabel: string;
  toConfirmLogout: boolean;
  toAbout: boolean;
  toConfirmPassowrd: boolean;
  successPasswordmessage: boolean;
  passwordResetSuccessful: string;

  subscriptions: Subscription[];
  @ViewChild('menu', { static: true }) matMenu: MatMenu;
  @ViewChild('profile', { static: true }) public profileAnchor: ElementRef;
  @ViewChild('profilePopup', { read: ElementRef, static: true })
  public profilePopup: ElementRef;

  /**
   * Creates an instance of main header component.
   * @param configurationService
   * @param router
   * @param errorTermsStoreService
   * @param localeAppResourcesService
   * @param dialog
   * @param snackBar
   * @param helpService
   * @param logoutService
   * @param dialogService
   * @param serverStatusService
   */
  constructor(
    private logoutService: LogoutService,
    private configurationService: ConfigurationService,
    private router: Router,
    private errorTermsStoreService: ErrorTermsStoreService,
    private localeAppResourcesService: LocaleAppResourcesService,
    private dialog: MatDialog,
    private snackBar: MatSnackBar,
    private helpService: HelpService,
    private dialogService: DialogService,
    private serverStatusService: ServerStatusService
  ) {
    this.headerTabClicked = new EventEmitter();
  }

  ngOnInit() {
    this.subscriptions = [];
    this.setUserProfile();
    this.show = false;
    this.toConfirmLogout = false;
    this.toAbout = false;
    this.successPasswordmessage = false;
    this.LogoutMessage1 = this.errorTermsStoreService.getErrorMessage(
      'LogoutMessage1'
    );
    this.LogoutMessage2 = this.errorTermsStoreService.getErrorMessage(
      'LogoutMessage2'
    );
    this.passwordResetSuccessful = this.errorTermsStoreService.getErrorMessage(
      'PasswordResetSuccessful'
    );
    this.OkLabel = this.localeAppResourcesService.getResource('OkLabel');
    this.CancelLabel = this.localeAppResourcesService.getResource(
      'CancelLabel'
    );
  }

  ngAfterViewInit() {
    const widget = (<any>window).AppcuesWidget((<any>window).Appcues.user());
    widget.init('#my-widget', {
      position: 'left'
      // Optionally add a header and/or footer.
      // header: '<h2>Study Optimizer</h2>',
      // footer: '<p>Sample Footer</p>'
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.userProfile) {
      this.setUserProfile();
    }
  }

  /**
   * Sets user profile
   */
  private setUserProfile() {
    if (this.userProfile !== undefined) {
      this.userInitials = AppUtil.getNameInitials(
        this.userProfile.$name,
        this.userProfile.$nameSeparator
      );
      /** Add below code to implement requirement of SO-19533 **/
      const UserArr = this.userProfile.$name.split(' ');
      this.UserNameToolTip = UserArr.join('\n');
      if (UserArr.length > 1) {
        this.firstName = UserArr[0];
        delete UserArr[0];
        this.lastName = UserArr.join(' ');
      } else {
        this.firstName = UserArr.join(' ');
      }
      this.profileItems = this.userProfile.$items;
    } else {
      this.userInitials = '';
      this.UserNameToolTip = '';
      this.profileItems = [];
    }
  }

  // toggleProfile(value?: boolean) {
  //   if (value !== undefined) {
  //     this.show = value;
  //   } else {
  //     this.show = !this.show;
  //   }
  // }

  /**
   * Determines whether tab click on
   * @param moduleName
   */
  onTabClick(moduleName: string) {
    this.canDeactivate().subscribe(res => {
      if (res === true) {
        this.headerTabClicked.emit(moduleName);
      }
    });
  }
  /**
   * Determines whether deactivate can
   * @param [isReset]
   * @returns deactivate
   */
  canDeactivate(isReset?: boolean): Observable<boolean> {
    if (isReset === true) {
      return of(true);
    }
    if (this.dialogService.isStatusChanged === true) {
      return this.dialogService.confirm();
    }
    return of(true);
  }

  /**
   * Gets resource
   * @param key
   * @returns resource
   */
  private getResource(key: string): string {
    return this.localeAppResourcesService.getResource(key);
  }

  /**
   * Gets action app resource
   * @returns action app resource
   */
  private getActionAppResource(): HeaderClickAction {
    return {
      about: this.getResource('HeaderAboutStudyOptimizer'),
      logout: this.getResource('HeaderLogout'),
      help: this.getResource('HeaderProfileHelp'),
      password: this.getResource('HeaderChangePassword')
    };
  }

  /**
   * Determines whether click action on
   * @param item
   */
  onClickAction(item: string) {
    if (item) {
      // this.toggleProfile();
      if (this.getActionAppResource().logout === item) {
        this.openConfirmLogoutDialog();
      } else if (this.getActionAppResource().about === item) {
        this.openAboutDialog();
      } else if (this.getActionAppResource().password === item) {
        this.openPasswordDialog();
      } else if (this.getActionAppResource().help === item) {
        this.helpService.openHelp();
      } else {
        this.router.navigate([item.toLowerCase()]);
      }
    }
  }

  /**
   * Opens confirm logout dialog
   */
  openConfirmLogoutDialog() {
    const logoutDialogRef = this.dialog.open(ConfirmationDialogComponent, {
      width: DIALOG_CONFIG.LOGOUT.WIDTH,
      data: this.getLogoutDialogProp(),
      panelClass: DIALOG_CONFIG.LOGOUT.PANEL_CLASS,
      id: DIALOG_CONFIG.LOGOUT.ID
    });
    this.subscriptions.push(
      logoutDialogRef.afterClosed().subscribe(eventObject => {
        if (eventObject && eventObject.action === this.getLogoutDialogProp().okLabel) {
          this.logout();
        }
      })
    );
  }

  /**
   * Gets logout dialog prop
   * @returns
   */
  getLogoutDialogProp() {
    const logoutMsg =
      this.errorTermsStoreService.getErrorMessage('LogoutMessage1') +
      '<br>' +
      this.errorTermsStoreService.getErrorMessage('LogoutMessage2') +
      '<br>';
    return {
      heading: '',
      message: logoutMsg,
      prefixMaterialIcon: {
        name: 'help',
        color: '#0e6ca9',
        size: '65px'
      },
      okLabel: this.localeAppResourcesService.getResource('OkLabel'),
      cancelLabel: this.localeAppResourcesService.getResource('CancelLabel')
    };
  }

  /**
   * Logouts main header component
   */
  logout() {
    const actionParam: Action = {};
    // TODO
    const subscription = this.logoutService.logout().subscribe(data => {
      if (data.result === COMMON.RESULT_OK) {
        if (this.serverStatusService.serverStatus) {
          if (this.serverStatusService.serverStatus.loginRequired) {
            const loginUrl = this.configurationService.$loginUrl;
            if (loginUrl) {
              AppWindow.getWindow().location.href = loginUrl;
            }
          } else {
            AppWindow.getWindow().location.href = this.serverStatusService.serverStatus.ssoRedirectURL;
          }
        }
      } else if (data.result === COMMON.RESULT_ERROR) {
        console.log(data.error.message);
        throw new Error(data.error.message);
      }
    });
    this.subscriptions.push(subscription);
  }

  /**
   * Opens about dialog
   */
  openAboutDialog() {
    const logoutDialogRef = this.dialog.open(ConfirmationDialogComponent, {
      width: DIALOG_CONFIG.ABOUT.WIDTH,
      data: this.getAboutDialogProp(),
      panelClass: DIALOG_CONFIG.ABOUT.PANEL_CLASS,
      id: DIALOG_CONFIG.ABOUT.ID
    });
  }

  getAboutDialogProp() {
    return {
      heading: this.aboutResource.aboutBoxTitle,
      message:
        this.aboutResource.aboutBoxVersion +
        '<br><br>' +
        this.aboutResource.aboutBoxDisclaimer,
      postfixImageUrl: this.aboutResource.logoUrl,
      okLabel: this.localeAppResourcesService.getResource('OkLabel'),
      showCancel: false
    };
  }

  openPasswordDialog() {
    const passwordDialogRef = this.dialog.open(PasswordComponent, {
      width: DIALOG_CONFIG.PASSWORD.WIDTH,
      data: this.getPasswordDialogProp(),
      panelClass: DIALOG_CONFIG.PASSWORD.PANEL_CLASS,
      id: DIALOG_CONFIG.PASSWORD.ID
    });
    this.subscriptions.push(
      passwordDialogRef.afterClosed().subscribe(eventObject => {
        if (eventObject) {
          console.log(eventObject.data);
          this.showBanner(
            'positive',
            'positive-snackbar',
            'assets/images/manageplans/icon-positive.png',
            'PasswordResetSuccessful'
          );
        }
      })
    );
  }

  /**
   * Gets password dialog prop
   * @returns
   */
  getPasswordDialogProp() {
    return {
      heading: this.passwordResource.changePassword,
      currentPassword: this.passwordResource.currentPassword,
      nonEmptyField: this.passwordResource.nonEmptyField,
      newPassword: this.passwordResource.newPassword,
      confirmPassword: this.passwordResource.confirmPassword,
      passwordMisMatchMessage: this.passwordResource.passwordMisMatchMessage,
      lengthRule: this.passwordResource.lengthRule,
      mixCharRule: this.passwordResource.mixCharRule,
      okLabel: this.localeAppResourcesService.getResource('OkLabel'),
      cancelLabel: this.localeAppResourcesService.getResource('CancelLabel')
    };
  }

  /**
   * Shows banner
   * @param messageType
   * @param panelClass
   * @param iconPath
   * @param messageCode
   */
  showBanner(messageType, panelClass, iconPath, messageCode) {
    this.snackBar.openFromComponent(DisplayBannerComponent, {
      data: {
        message: this.errorTermsStoreService.getErrorMessage(messageCode),
        icon: iconPath,
        messageType: messageType
      },
      panelClass: [panelClass]
    });
  }

  /**
   * Onclicks window
   */
  onclickWindow() {
    console.log('clicked');
  }

  /**
   * Hides image
   * @param event
   */
  hideImage(event) {
    this.hideCustomerLogo = true;
  }

  // @HostListener('keydown', ['$event'])
  // public keydown(event: any): void {
  //   if (event.keyCode === 27) {
  //     this.toggleProfile(false);
  //   }
  // }

  // @HostListener('document:click', ['$event'])
  // public documentClick(event: any): void {
  //   if (!this.contains(event.target)) {
  //     this.toggleProfile(false);
  //   }
  // }

  /**
   * Contains main header component
   * @param target
   * @returns true if contains
   */
  private contains(target: any): boolean {
    return (
      this.profileAnchor.nativeElement.contains(target) ||
      (this.profilePopup
        ? this.profilePopup.nativeElement.contains(target)
        : false)
    );
  }

  ngOnDestroy() {
    this.items = null;
    this.userProfile = null;
    this.passwordComponent = null;
    this.profileItems = null;
    this.subscriptions.forEach(subscription => {
      subscription.unsubscribe();
    });
    this.subscriptions = null;
  }
}
