import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { SafeHtml } from '@angular/platform-browser';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'ctos-confirmation-dialog',
  templateUrl: './confirmation-dialog.component.html'
})
export class ConfirmationDialogComponent {
  constructor(
    public dialogRef: MatDialogRef<ConfirmationDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data
  ) {
    if (!data.okLabel) {
      data.okLabel = 'Ok';
    }
    if (!data.cancelLabel) {
      data.cancelLabel = 'Cancel';
    }
  }

  /**
   * Cancels confirmation dialog component
   * @param btnLabel
   */
  cancel(btnLabel) {
    this.dialogRef.close({ action: btnLabel });
  }

  /**
   * confirmation dialog component
   * @param btnLabel
   */
  ok(btnLabel) {
    this.dialogRef.close({ action: btnLabel });
  }

  /**
   * Shows cancel
   * @returns true if cancel
   */
  showCancel(): boolean {
    const data: any = this.data;
    if (data && data.hasOwnProperty('showCancel')) {
      return data.showCancel;
    }
    return true;
  }

  // isDataHtml() {
  //   const data: any = this.data;
  //   if (data && data.hasOwnProperty('isDataHtml')) {
  //     return data.isDataHtml;
  //   }
  //   return false;
  // }
}
