import { Injectable } from '@angular/core';

import { Encryption } from '../models/cache/encryption';
import { EnvironmentParam } from './../models/environment-param';
import { EncryptionService } from './cache/encryption.service';

@Injectable({
  providedIn: 'root'
})
export class ConfigurationService {

  private encryptionKey: string;
  private cacheAge: number;
  private encryptionType: Encryption;
  private dispatchedUrl: string;
  private applicationUrl: string;
  private loginUrl: string;
  private _authUrl: string;
  private _soApiUrl: string;


  /**
   * Gets encrypt key
   */
  get encryptKey(): string {
    return this.encryptionKey;
  }

  /**
   * Sets encrypt key
   */
  set encryptKey(v: string) {
    this.encryptionKey = v;
    this.encryptionService.setKey(this.encryptionKey);
  }

  /**
   * Gets cache max age
   */
  get cacheMaxAge(): number {
    return this.cacheAge;
  }

  /**
   * Sets cache max age
   */
  set cacheMaxAge(v: number) {
    this.cacheAge = v;
  }

  /**
   * Gets encrypt type
   */
  get encryptType(): Encryption {
    return this.encryptionType;
  }

  /**
   * Sets encrypt type
   */
  set encryptType(v: Encryption) {
    this.encryptionType = v;
    this.encryptionService.setEncryption(this.encryptionType);
  }

  /**
   * Gets dispatch url
   */
  get dispatchUrl(): string {
    return this.dispatchedUrl;
  }

  /**
   * Sets dispatch url
   */
  set dispatchUrl(v: string) {
    this.dispatchedUrl = v;
  }

  /**
   * Gets app url
   */
  get appUrl(): string {
    return this.applicationUrl;
  }

  /**
   * Sets app url
   */
  set appUrl(v: string) {
    this.applicationUrl = v;
  }

  /**
     * Getter $loginUrl
     * @return string
     */
  public get $loginUrl(): string {
    return this.loginUrl;
  }

  /**
   * Setter $loginUrl
   * @param string value
   */
  public set $loginUrl(value: string) {
    this.loginUrl = value;
  }

  /**
   * Gets app url
   */
  get authUrl(): string {
    return this._authUrl;
  }

  /**
   * Sets app url
   */
  set authUrl(v: string) {
    this._authUrl = v;
  }

  /**
   * Gets app url
   */
  get soApiUrl(): string {
    return this._soApiUrl;
  }

  /**
   * Sets app url
   */
  set soApiUrl(v: string) {
    this._soApiUrl = v;
  }


  /**
   * Creates an instance of configuration service.
   * @param encryptionService
   */
  constructor(private encryptionService: EncryptionService) {
  }


  /**
   * Sets config
   *
   * Set the application configuration
   * encryptionKey
   * cacheAge
   * encryptionType
   * applicationUrl
   * @param environment
   */
  setConfig(environment?: any | EnvironmentParam) {
    if (environment) {
      if (!this.typeCheck(environment)) {
        throw new Error(`Invalid environment object`);
      }
      this.encryptKey = environment.clientEncryptionKey;
      this.cacheMaxAge = environment.cacheAge;
      this.encryptType = environment.cryptoJS;
      this.appUrl = environment.app_url;
      this.$loginUrl = environment.login_url;
      this.authUrl = environment.authUrl;
      this.soApiUrl = environment.soApiUrl;
    } else {
      throw new Error(`Environment object is empty`);
    }
  }

  private typeCheck(obj: any): obj is EnvironmentParam {
    return (obj.clientEncryptionKey !== undefined && obj.cacheAge !== undefined
      && obj.cryptoJS !== undefined && obj.app_url !== undefined && obj.login_url !== undefined);
  }



}
