import { DatePipe } from '@angular/common';
import { Injectable } from '@angular/core';

import * as XLSX from 'xlsx';

import { LocaleAppResourcesService } from '../services/locale-app-resources.service';
import { Index } from '../utils/index.enum';
import { Observable } from 'rxjs';
import { empty } from 'rxjs';
import { saveAs } from 'file-saver';
import { EMPTY } from 'rxjs';
declare const html2canvas: any;
@Injectable({
  providedIn: 'root'
})
export class PCEService {
  private exportObj: any = {};

  header: any;
  constructor(
    private datePipe: DatePipe,
    private localAppResource: LocaleAppResourcesService
  ) {}

  // copy
  setDataForCopy(dataArray) {
    const headerData = this.jsonfyPlanCardHeaderData(this.header);
    let copyString = this.convertArrayToString(headerData, true);
    copyString += this.convertArrayToString(dataArray);
    copyString += this.appendFooter(copyString);
    this.setTextToCopy(copyString);
  }
  jsonfyPlanCardHeaderData(data) {
    const result = [];
    for (const key in data) {
      if (data.hasOwnProperty(key)) {
        result.push({
          A: key,
          B: data[key]
        });
      }
    }
    return result;
  }
  planCardHeaderData(header?) {
    if (header) {
      this.header = header;
    }
  }
  convertArrayToString(data, hideTopRow = false) {
    let copystring = '';
    if (!hideTopRow) {
      copystring += this.setRowHeader(data[0]);
    }
    for (let j = 0; j < data.length; j++) {
      for (const key in data[j]) {
        if (data[j].hasOwnProperty(key)) {
          copystring += '"' + data[j][key] + '"\t';
        }
      }
      copystring += '\r\n';
    }
    copystring += '\r\n';
    return copystring;
  }
  setRowHeader(data) {
    let str = '';
    for (const key in data) {
      if (data.hasOwnProperty(key)) {
        str += '"' + key + '"\t';
      }
    }
    str += '\n';
    return str;
  }
  appendFooter(copystring) {
    return '\r\n' + this.getFooter();
  }
  getFooter() {
    const FOOTER = this.localAppResource.getResource('FooterForReports');
    return FOOTER;
  }
  setTextToCopy(data) {
    let success = false;
    let tmpElem = document.createElement('textarea');
    tmpElem.style.position = 'fixed';
    tmpElem.style.top = '0px';
    tmpElem.style.left = '-99999px';
    tmpElem.setAttribute('readonly', '');
    tmpElem.value = data;
    document.body.appendChild(tmpElem);
    tmpElem.select();
    tmpElem.focus();
    tmpElem.setSelectionRange(0, tmpElem.value.length);
    let succeeded;
    try {
      succeeded = window.document.execCommand('copy');
      success = true;
      console.warn('copy success status:', succeeded);
      if (!succeeded) {
        throw succeeded;
      }
    } catch (err) {
      success = false;
    }
    document.body.removeChild(tmpElem);
    tmpElem = null;
    return success;
  }

  // export
  exportData(components?: any) {
    if (components.length > 0) {
      Promise.all([
        components[Index.FIRST].workbookOptions(),
        components[Index.SECOND].workbookOptions(),
        components[Index.THIRD].workbookOptions()
      ]).then(workbooks => {
        workbooks[Index.FIRST].sheets[Index.FIRST].rows = workbooks[
          Index.FIRST
        ].sheets[Index.FIRST].rows.concat(
          [{}],
          workbooks[Index.SECOND].sheets[Index.FIRST].rows,
          workbooks[Index.THIRD].sheets[Index.FIRST].rows
        );
        components[Index.FIRST].save(workbooks[Index.FIRST]);
      });
    }
  }

  exportExcelData(data?: any, fileName?: string) {
    const header = this.header;
    const tempHeader = this.jsonfyPlanCardHeaderData(header);
    const tempFooterArray = [];
    const footerData = this.getFooter();
    tempFooterArray.push({ ' ': footerData });
    const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(tempHeader, {
      skipHeader: true
    });
    const wb: XLSX.WorkBook = XLSX.utils.book_new();
    const headerCount = Object.keys(header).length + 2;
    XLSX.utils.sheet_add_json(worksheet, data, { origin: 'A' + headerCount });
    XLSX.utils.sheet_add_json(worksheet, tempFooterArray, {
      skipHeader: true,
      origin: -1
    });
    XLSX.utils.book_append_sheet(wb, worksheet, 'Sheet1');
    XLSX.writeFile(wb, fileName);
  }

  // print
  setDataForPrint(dataArray) {
    const dataHtml = this.createDataTableForPrint(dataArray);
    const fntz = '10px';
    const htmlstr =
      this.getPlanCardHeaderForPrint(fntz) +
      dataHtml +
      this.getPlanCardFooterForPrint(fntz);
    this.doPrintFrame(htmlstr);
  }
  execPCECommand(command, eleW, name): Observable<any> {
    switch (command.action) {
      case 'ExportGraph':
        return this.exportGraph(command, eleW, name);
      case 'PrintGraph':
        return this.printGraph(command);
      case 'CopyGraph':
        return this.copyGraph(command, name);
      case 'copyCohortData':
        return this.copyCohort(command);
    }
    return EMPTY;
  }
  copyGraph(cmd, name): Observable<any> {
    return new Observable(observer => {
      if (name === 'copy') {
        let headerData = this.jsonfyPlanCardHeaderData(cmd.header);
        let copyString = this.convertArrayToString(headerData, true);
        copyString += 'Milestones\t\r\n\r\n';
        headerData = this.jsonfyPlanCardHeaderData(cmd.metaData.milestone);
        copyString += this.convertArrayToString(headerData, true);
        copyString += 'Chart Data:\t\r\n\r\n';
        copyString += this.convertArrayToString(cmd.metaData.curve);
        copyString += this.appendFooter(copyString);
        this.setTextToCopy(copyString);
        cmd.copyString = copyString;
        observer.next(cmd);
      }
    });
  }

  copyCohort(cmd): Observable<any> {
    return new Observable(observer => {
      const headerData = this.jsonfyPlanCardHeaderData(cmd.header);
      let copyString = this.convertArrayToString(headerData, true);
      if (cmd.metaData.cohortData.length > 0) {
        copyString += 'Cohort\t\r\n\r\n';
        copyString += this.convertArrayToString(cmd.metaData.cohortData);
      }
      if (cmd.metaData.dependencyData.length > 0) {
        copyString += 'Dependency\t\r\n\r\n';
        copyString += this.convertArrayToString(cmd.metaData.dependencyData);
      }
      copyString += this.appendFooter(copyString);
      this.setTextToCopy(copyString);
      observer.next(cmd);
    });
  }

  printGraph(cmd): Observable<any> {
    return new Observable(observer => {
      html2canvas(cmd.domElement).then(canvas => {
        const dataHtml = '<img src="' + canvas.toDataURL() + '">';
        const headerStr = this.getHeaderAsHtmlStr(cmd.header);
        const fntz = '10px';
        const htmlstr =
          headerStr + dataHtml + this.getPlanCardFooterForPrint(fntz);
        this.doPrintFrame(htmlstr);
        observer.next(cmd);
      });
    });
  }
  exportGraph(cmd, eleW, name): Observable<any> {
    return new Observable(observer => {
      html2canvas(cmd.domElement).then(canvas => {
        const fntz = '10px';
        const imgsrc = this.getHeaderAsHtmlStr(cmd.header);
        let windowContent = '<div >';
        windowContent += imgsrc;
        windowContent += '<div><img src="' + canvas.toDataURL() + '"></div>';
        windowContent += this.getPlanCardFooterForPrint(fntz);
        windowContent += '</div>';
        const tmpDiv = document.createElement('div');
        tmpDiv.setAttribute('id', 'dummyexportDiv');
        tmpDiv.innerHTML = windowContent;
        // style the div so that html2canvas can find it...
        console.log('html2canvas', windowContent);
        tmpDiv.style.position = 'absolute';
        tmpDiv.style.left = '0px';
        tmpDiv.style.top = '0px';
        tmpDiv.style.zIndex = '-1';
        tmpDiv.style.backgroundColor = 'white';
        tmpDiv.style.padding = '45px';
        document.body.appendChild(tmpDiv);
        html2canvas(tmpDiv).then(cnvs => {
          console.log(canvas.toDataURL());
          this.downloadCanvas(cnvs, 'png', name, this.save);
          observer.next(cmd);
        });
      });
    });
  }
  downloadCanvas(canvas, format, name, callback) {
    canvas.toBlob(function(blob) {
      const filesize = Math.round(blob[length] / 1024) + ' KB';
      if (callback) {
        callback(blob, filesize, name);
      }
    });
  }
  save(dataBlob, filesize, name) {
    saveAs(dataBlob, name + '.png'); // FileSaver.js function
    const ref = document.getElementById('dummyexportDiv');
    ref.parentNode.removeChild(ref);
  }
  createDataTableForPrint(data) {
    let tempstring = '<div style="margin-top:20px;"><table>';
    tempstring += this.createHeaderForPrint(data[0]);
    tempstring += '<tbody>';
    for (let j = 0; j < data.length; j++) {
      tempstring += '<tr>';
      for (const key in data[j]) {
        if (data[j].hasOwnProperty(key)) {
          tempstring += '<td>' + data[j][key] + '</td>';
        }
      }
      tempstring += '</tr>';
    }
    tempstring += '</tbody></table></div>';
    return tempstring;
  }
  createHeaderForPrint(data) {
    let str = '<thead><tr>';
    for (const key in data) {
      if (data.hasOwnProperty(key)) {
        str += '<th>' + key + '</th>';
      }
    }
    str += '</tr></thead>';
    return str;
  }
  getHeaderAsHtmlStr(data) {
    let str =
      '<div class="header" style="font: 11pt sans-serif;color:"#000" font-size:${fntz}">' +
      '<table>';
    for (const key in data) {
      if (data.hasOwnProperty(key)) {
        str +=
          '<tr>' +
          '  <td> ' +
          key +
          '</td>' +
          '<td>' +
          data[key] +
          '</td>' +
          '</tr>';
      }
    }
    str += '</table></div>';
    return str;
  }
  getPlanCardHeaderForPrint(fntz) {
    const headerData = this.header;
    console.log(headerData);
    let headerStr = '';

    for (const key in headerData) {
      if (headerData.hasOwnProperty(key)) {
        headerStr +=
          '<tr>' + `<td>${key}</td>` + ` <td>${headerData[key]}</td>` + '</tr>';
      }
    }
    return (
      '<div class="header" style="font: 11pt sans-serif; font-size:${fntz}">' +
      '<table>' +
      headerStr +
      '</table>' +
      '</div>'
    );
  }
  getPlanCardFooterForPrint(fntz) {
    let footerData = this.getFooter();
    footerData = footerData.replace(
      '<Organization Name>',
      '&lt;Organization Name&gt;'
    );
    return `<div style="text-align:center; font-size: 14px"><p><strong> ${footerData} </strong></p></div>`;
  }
  // doPrintFrame(htmlstr) {
  //    htmlstr = htmlstr + this.appendStyleForTable();
  //   const frame1 = document.createElement('iframe');
  //   frame1.name = 'frame1';
  //   frame1.style.position = 'absolute';
  //   frame1.style.top = '-1000000px';
  //   document.body.appendChild(frame1);
  //   const frameDoc = frame1.contentWindow ? frame1.contentWindow :
  //     frame1.contentDocument['document'] ? frame1.contentDocument['document'] : frame1.contentDocument;
  //   frameDoc.document.open();
  //   frameDoc.document.write(htmlstr);
  //   frameDoc.document.close();
  //   if (frameDoc.queryCommandSupported && frameDoc.queryCommandSupported('print')) {
  //     frameDoc.execCommand('print', false, null);
  //   } else {
  //     // window.frames['frame1'].focus();
  //     // window.frames['frame1'].print();
  //     frame1.contentWindow.document.write(htmlstr);
  //     frame1.contentWindow.print();
  //   }

  // }
  doPrintFrame(htmlstr) {
    const ua = window.navigator.userAgent;
    const msie = ua.indexOf('MSIE');
    if (msie > 0 || !!navigator.userAgent.match(/Trident.*rv\:11\./)) {
      htmlstr = htmlstr + this.appendStyleForTable(true);
    } else {
      htmlstr = htmlstr + this.appendStyleForTable(false);
    }
    htmlstr = htmlstr + this.appendStyleForTable();
    const frame1 = document.createElement('iframe');
    frame1.name = 'frame1';
    frame1.style.position = 'absolute';
    frame1.style.top = '-1000000px';
    document.body.appendChild(frame1);
    const frameDoc = frame1.contentWindow
      ? frame1.contentWindow
      : frame1.contentDocument['document']
      ? frame1.contentDocument['document']
      : frame1.contentDocument;
    frameDoc.document.open();
    frameDoc.document.write(htmlstr);
    frameDoc.document.close();
    window.setTimeout(() => {
      if (msie > 0 || !!navigator.userAgent.match(/Trident.*rv\:11\./)) {
        const result = frame1.contentWindow.document.execCommand(
          'print',
          false,
          null
        );
        if (!result) {
          window.frames['frame1'].contentWindow.print();
        }
      } else {
        frame1.contentWindow.print();
      }
    }, 1000);
  }
  appendStyleForTable(type?: boolean) {
    // tslint:disable-next-line:max-line-length
    // return '<style> @media all { table,th,td{ border: 1px solid black; border-collapse: collapse  }  table{ page-break-after: auto; width:100%; margin-top:10px;font-size:0.3vw;counter-reset: page 1;}tr {page-break-inside: avoid;page-break-after: always}td:not(:first-child){padding:1px; white-space: nowrap; }table { page-break-inside: auto}tr { page-break-inside: auto;page-break-after: auto}thead {display: table-header-group}tfoot {display: table-footer-group}@media all and (-webkit-min-device-pixel-ratio:0) {<style> table,th,td{ border: 1px solid black; border-collapse: collapse  }  table{ page-break-after: auto; width:100%; margin-top:10px;font-size:0.8vw;} tr{page-break-inside: avoid;page-break-after: auto} td:not(:first-child){padding:1px; white-space: nowrap; } table{ page-break-inside: auto} tr{ page-break-inside: auto;page-break-after: auto} thead{display: table-header-group} tfoot{display: table-footer-group </style>';
    if (type) {
      // tslint:disable-next-line:max-line-length
      return '<style> @page {margin:1.5cm 0.4cm;} @media all { table,th,td{ border: 1px solid black; border-collapse: collapse  } .header td{width:50%;}table{ page-break-after: auto; width:100%; margin-top:10px;font-size:0.386vw;counter-reset: page 1;}tr {page-break-inside: avoid;page-break-after: always}td {padding:1px; white-space:wrap; max-width:10px; width:10px;word-wrap:break-word; }td:first-child{max-width:30px; width:30px;} th{ width:10px; word-wrap: break-word;} table { page-break-inside: auto}tr { page-break-inside: auto;page-break-after: auto}thead {display: table-header-group}tfoot {display: table-footer-group} } </style>';
    } else {
      // tslint:disable-next-line:max-line-length
      return '<style>@media all and (-webkit-min-device-pixel-ratio:0) { table,th,td{ border: 1px solid black; border-collapse: collapse  } .header td{width:50%;} table{ page-break-after: auto; width:100%; margin-top:10px;font-size:0.8vw;} tr{page-break-inside: avoid;page-break-after: auto} td:not(:first-child){padding:1px; white-space: nowrap; } table{ page-break-inside: auto} tr{ page-break-inside: auto;page-break-after: auto} thead{display: table-header-group} tfoot{display: table-footer-group} </style>';
    }
  }
}
