import { Directive, HostBinding, Input, Optional } from '@angular/core';
import { ControlContainer } from '@angular/forms';

@Directive({
    selector: 'label[ctosControlName]',
})
export class LabelControlDirective {
    @Input() ctosControlName: string;

    constructor(@Optional() private parent: ControlContainer) { }

    /**
     * Gets host binding
     */
    @HostBinding('textContent')
    get controlValue() {
        return this.parent ? this.parent.control.get(this.ctosControlName).value : '';
    }
}
