import { Injectable } from '@angular/core';
import { MatDialog } from '@angular/material';
import { map, } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { ALERT_TYPE, DIALOG_CONFIG } from '../utils/service-constant';
import { LocaleAppResourcesService } from './locale-app-resources.service';
import { ConfirmationDialogComponent } from '../components/confirmation-dialog/confirmation-dialog.component';

export interface DialogsProp {
  heading?: string;
  message?: string;
  okLabel?: string;
  cancelLabel?: string;
  showCancel?: boolean;
}

@Injectable({
  providedIn: 'root'
})
export class DialogService {
  private _isStatusChanged: boolean;

  public get isStatusChanged(): boolean {
    return (this._isStatusChanged === undefined ? false : this._isStatusChanged);
  }

  public set isStatusChanged(v: boolean) {
    this._isStatusChanged = v;
  }

  private _selectedStudyKey: any;

  public get selectedStudyKey(): any {
    return this._selectedStudyKey;
  }

  private _isProceed: boolean;


  public get isProceed(): boolean {
    return this._isProceed;
  }


  public set isProceed(v: boolean) {
    this._isProceed = v;
  }



  public set selectedStudyKey(v: any) {
    this._selectedStudyKey = v;
  }

  constructor(private localeAppResourcesService: LocaleAppResourcesService,
    private dialog: MatDialog) {
  }

  confirm(): Observable<boolean> {
    let confirmation = false;
    const dialogRef = this.showUnsavedDataConfirmation();
    return dialogRef.afterClosed().pipe(map(res => {
      const eventObject = res;
      if (eventObject && eventObject.action === this.getConfirmProp().okLabel) {
        confirmation = true;
      } else if (eventObject && eventObject.action === this.getConfirmProp().cancelLabel) {
        confirmation = false;
      }
      return confirmation;
    }));
  }

  private showUnsavedDataConfirmation(data?: any) {
    data = data || this.getConfirmProp();
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      width: DIALOG_CONFIG.CONFIRM_UNSAVED_DATA.WIDTH,
      data: data,
      panelClass: DIALOG_CONFIG.DELETE_PLAN.PANEL_CLASS,
      id: 'warnDataLossOnNavDialog'
    });
    return dialogRef;
  }

  private getConfirmProp() {
    return {
      heading: this.localeAppResourcesService.getResource('unsavedTitle'),
      message: this.localeAppResourcesService.getResource('unsavedMessage'),
      okLabel: this.localeAppResourcesService.getResource(ALERT_TYPE.OkLabel),
      cancelLabel: this.localeAppResourcesService.getResource(
        ALERT_TYPE.CancelLabel
      )
    };
  }
}
