import { AfterViewInit, Component, Input, OnChanges, SimpleChanges, ViewChild } from '@angular/core';

import { PCEService } from '../../services/pce.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'ctos-export',
  templateUrl: './export.component.html'
})
export class ExportComponent implements OnChanges, AfterViewInit {

  @Input() startExport;
  @Input() exportToExcelObj: any;
  public headerCells: any = {
    background: '#ffffff',
    color: '#ffffff'
  };
  public cellAlignment: any = {
    textAlign: 'left'
  };

  exportToExcelObj1: any;
  @ViewChild('excelexportdata', { static: true }) excelexportdata;
  @ViewChild('excelexportheader', { static: true }) excelexportheader;
  @ViewChild('excelexportfooter', { static: true }) excelexportfooter;

  count = 0;

  ngOnChanges(changes: SimpleChanges) {
    this.count = this.count + 1;
    if (this.startExport && this.count > 1) {
      if (changes.exportToExcelObj.currentValue.data) {
        this.excelexportdata.data = changes.exportToExcelObj.currentValue.data;
      }
      this.pceService.exportData([this.excelexportheader, this.excelexportdata, this.excelexportfooter]);
    }
  }

  constructor(private pceService: PCEService) {
  }

  ngAfterViewInit() {
    if (this.startExport) {
      this.pceService.exportData([this.excelexportheader, this.excelexportdata, this.excelexportfooter]);
    }
  }
}
