import * as CryptoJS from 'crypto-js';

import { Encryption } from './encryption';

/**
 * Crypto jsencryption
 */
export class CryptoJSEncryption implements Encryption {
    encrypt(obj: Object, key: string): string {
        const text = JSON.stringify(obj);
        // const key = CryptoJS.enc.Base64.parse('TEST1234');
        const encrypted = CryptoJS.AES.encrypt(text, key);
        return encrypted.toString();
    }
    decrypt(text: Object, key: string): Object {
        // const key = CryptoJS.enc.Base64.parse('TEST1234');
        const decrypted = CryptoJS.AES.decrypt(text.toString(), key);
        const obj = JSON.parse(decrypted.toString(CryptoJS.enc.Utf8));
        return obj;
    }
}
