import {
  Component,
  OnInit,
  Input,
  ViewChild,
  Output,
  EventEmitter,
  ViewEncapsulation
} from '@angular/core';
import { MatMenu, MenuPositionX, MenuPositionY } from '@angular/material';
@Component({
  // tslint:disable-next-line:component-selector
  selector: 'ctos-context-menu',
  templateUrl: './context-menu.component.html',
  encapsulation: ViewEncapsulation.None
})
export class ContextMenuComponent implements OnInit {
  /**
   * Creates an instance of context menu component.
   */
  constructor() { }
  private _options;
  @ViewChild('menu', { static: true }) matMenu: MatMenu;
  @Output() fireEvent: EventEmitter<string> = new EventEmitter();
  @Input() contextMenuClass?: string;
  @Input() xPosition?: MenuPositionX;
  @Input() yPosition?: MenuPositionY;
  @Input() heading?: string;

  ngOnInit() {
  }

  @Input()
  set options(optionList: any[]) {
    this._options = optionList;
  }

  /**
   * Gets options
   */
  get options() {
    return this._options;
  }

  /**
   * Menus click
   * @param name
   */
  menuClick(name) {
    this.fireEvent.emit(name);
  }
}
