import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { CommonRoutes } from 'common-library';
import { UserDataService, WindowService } from 'so-common-library';
import { from, fromEvent, merge } from 'rxjs';

import { COEFFICIENT } from './app.constant';
import { environment } from 'environments/environment';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'ctos-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

  constructor(private router: Router,
    private userDataService: UserDataService,
    private windowService: WindowService) {
    if (environment.production) {
      this.router.config.unshift(
        ...CommonRoutes.data
      );
      this.windowService.redirectAsPerParams();
    }
    // this.appBootstrapService.init();
    // this.activityEvents();
  }

  ngOnInit(): void {
    // Initialize Appcues by identifying current user
    (<any>window).Appcues.identify(this.userDataService.getUserId(), { // Replace with unique identifier for current user
      name: this.userDataService.getUserName(),   // Current user's name
      email: this.userDataService.getUserName(), // Current user's email
      created_at: 1551171156765,    // Unix timestamp of user signup date
    });

    // Subscribe to router events
    this.router.events.subscribe((res) => {
      // check for NavigationEnd event to notify Appcues about 'page change'.
      if (res instanceof NavigationEnd) {
        if ((<any>window).Appcues) { // casting window as any to avoid, TypeScript compilation error.
          (<any>window).Appcues.page();
        }
      }
    });
    // --- End of Appcues integration code. ---

    // this.userDataService.userIdealWatch();
    // this.userActiveEvents();
  }

  /**
   * Monitor user activity events and store it in user ideal observable
   */
  activityEvents() {
    this.userDataService.userIdeal$ = from(merge(
      // fromEvent(document, 'keydown'),
      // fromEvent(document, 'keyup'),
      fromEvent(document, 'click')));
  }

  /**
   * Monitor user activity events and call keep user alive api call periodically
   */
  userActiveEvents() {
    const clicks$ = fromEvent(document, 'click');
    clicks$.subscribe(e => this.userDataService.keepUserSessionAlive(COEFFICIENT));
  }
}
