import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { EncryptionAction } from '../utils/cache/encryption-action.enum';
import { StoreType } from '../utils/cache/store-type.enum';
import { Action } from './../models/action';
import { CacheKey } from './../utils/cache-key.enum';
import { CacheType } from './../utils/cache/cache-type.enum';
import { COMMON } from './../utils/service-constant';
import { BaseConnectionService } from './base-connection.service';
import { ErrorTermsStoreService } from './error-terms-store.service';
import { PayloadCommandService } from './payload-command.service';
import { APIEndPoints } from '../models/api-end-points.enum';

export interface TermsErrorParam {
    commandName: string;
    categoriesTerms: string;
    categoriesError: string;
}

@Injectable({
    providedIn: 'root'
})
export class ErrorTermsService {

    /**
     * Creates an instance of error terms service.
     * @param baseConnectionService
     * @param payloadCommandService
     * @param errorTermsStoreService
     */
    constructor(private baseConnectionService: BaseConnectionService, private payloadCommandService: PayloadCommandService
        , private errorTermsStoreService: ErrorTermsStoreService) { }


    /**
     * Gets error terminologies
     * @param params
     * @param [cacheType]
     * @returns error terminologies
     */
    getErrorTerminologies(params, actionParam?: Action): Observable<any> {
        if (!params) {
            throw new Error(`params can not be empty or undefined`);
        } if (!this.typeCheck(params)) {
            throw new Error(`Invalid params object`);
        }

        actionParam = !actionParam ? {} as Action : actionParam;

        actionParam.cacheKey = CacheKey.TermErrorKey;
        actionParam.cacheType = CacheType.sticky;
        actionParam.storeType = StoreType.Session;
        actionParam.encryption = EncryptionAction.encrypt;
        actionParam.cmdName = ''; // reset cmd name
        actionParam.xmlRequest = '';

        const moduleInfo = {
            moduleName: 'NEWPLANTAB'
        };

        if (actionParam.endPoint) {
            actionParam.url = APIEndPoints.BASE_TERMS;
        }

        const that = this;
        let errorTermsPayload;
        if (actionParam.endPoint) {
            errorTermsPayload = that.payloadCommandService.getApiRequest(params.commandName, params.categoriesError,
                null, moduleInfo);

            if (!that.errorTermsStoreService.errorTerminologies) {
                return that.baseConnectionService.send(errorTermsPayload, actionParam)
                    .pipe(map((response) => {
                        if (response.result === COMMON.RESULT_OK) {
                            that.errorTermsStoreService.createErrorCodeBase(response.terminologies);
                            return 'ok';
                        } else {
                            return 'failed';
                        }
                    }));
            } else {
                return Observable.create('ok');
            }
        } else {
            errorTermsPayload = that.payloadCommandService.getCommandDetails(params.commandName, params.categoriesError,
                null, null, moduleInfo);

            if (!that.errorTermsStoreService.errorTerminologies) {
                return that.baseConnectionService.sendRequest(errorTermsPayload, actionParam)
                    .pipe(map((response) => {
                        if (response.result === COMMON.RESULT_OK) {
                            that.errorTermsStoreService.createErrorCodeBase(response.terminologies);
                            return 'ok';
                        } else {
                            return 'failed';
                        }
                    }));
            } else {
                return Observable.create('ok');
            }
        }
    }


    private typeCheck(obj: any): obj is TermsErrorParam {
        return (obj.categoriesError !== undefined && obj.commandName !== undefined);
    }
}
