import { Injectable } from '@angular/core';
import { Observable, of, Subject, throwError } from 'rxjs';
import { tap } from 'rxjs/operators';

import { CacheContent } from '../../models/cache/cache-content';
import { Store } from '../../models/cache/store';
import { ConfigurationService } from '../configuration.service';

@Injectable({
  providedIn: 'root'
})
export class InMemoryService implements Store {
  private cache: Map<string, CacheContent> = new Map<string, CacheContent>();
  private inFlightObservables: Map<string, Subject<any>> = new Map<
    string,
    Subject<any>
  >();
  maxAge: number;

  /**
   * Creates an instance of in memory service.
   * @param config
   */
  constructor(private config: ConfigurationService) {
    this.maxAge = config.cacheMaxAge;
  }

  /**
   * Gets in memory service
   *
   * This method will check if cache key is available in cache
   * then it will return the value in observable form.
   * If key not present in cache then execute the this.post method
   * after the successful exection of  this.post observable method (which return the observable)
   * set the key and value in cache
   *
   * @param key
   * @param [callback]
   * @returns get
   */
  get(key: string, callback?: Observable<any>): Observable<any> | Subject<any> {
    if (this.hasValidCachedValue(key)) {
      return of(this.cache.get(key).value);
    }
    /*
    if (this.inFlightObservables.has(key)) {
      return this.inFlightObservables.get(key);
    } else if (callback && callback instanceof Observable) {
      this.inFlightObservables.set(key, new Subject());
      console.log(`%c Calling api in memory for ${key}`, 'color: purple');
      return callback.pipe(tap((value) => { this.set(key, value); }));
    } else {
      return throwError('Requested key is not available in Cache');
    }
   */
    if (callback && callback instanceof Observable) {
      console.log(`%c Calling api for ${key}`, 'color: purple');
      return callback.pipe(
        tap(value => {
          this.set(key, value);
        })
      );
    } else {
      return throwError('Requested key is not available in Cache');
    }
  }

  /**
   * Sets in memory service
   * @param key
   * @param value
   */
  set(key: string, value: any): void {
    // this.cache.set(key, { value: value, expiry: Date.now() + this.maxAge });
    this.cache.set(key, { value: value, expiry: 0 });
    // this.notifyInFlightObservers(key, value);
    /**Testing */
    console.log(`%c In Memory data`, `color: green`);
    console.log(this.cache);
  }

  /**
   * Has in memory service
   * @param key
   * @returns true if has
   */
  has(key: string): boolean {
    return this.cache.has(key);
  }

  /**
   * Calls in memory service
   * @param [fallback]
   * @returns call
   */
  call(fallback?: Observable<any>): Observable<any> | Subject<any> {
    if (fallback && fallback instanceof Observable) {
      return fallback.pipe(tap(value => value));
    }
  }

  /**
   * Determines whether valid cached value has
   * @param key
   * @returns true if valid cached value
   */
  private hasValidCachedValue(key: string): boolean {
    /*
    if (this.cache.has(key)) {
      if (this.cache.get(key).expiry < Date.now()) {
        this.cache.delete(key);
        return false;
      }
      return true;
    } else {
      return false;
    }*/
    let returnVal = false;
    if (this.cache.get(key)) {
      returnVal = true;
    } else {
      returnVal = false;
    }
    return returnVal;
  }

  /**
   * Notifys in flight observers
   * @param key
   * @param value
   */
  private notifyInFlightObservers(key: string, value: any): void {
    if (this.inFlightObservables.has(key)) {
      const inFlight = this.inFlightObservables.get(key);
      const observersCount = inFlight.observers.length;
      if (observersCount) {
        console.log(
          `%cNotifying ${
            inFlight.observers.length
          } flight subscribers for ${key}`,
          'color: Swap'
        );
        inFlight.next(value);
      }
      inFlight.complete();
      this.inFlightObservables.delete(key);
    }
  }

  /**
   * Removes in memory service
   * @param key
   */
  remove(key: string) {
    //  this.cache.clear();
    //  this.inFlightObservables.clear();
    if (this.cache.has(key)) {
      this.cache.delete(key);
    } else {
      this.cache.forEach((value, actualKey) => {
        if (actualKey.search(key)) {
          this.cache.delete(actualKey);
        }
      });
    }
    console.log(`%c In memory remove key ${key}`, `color:purple`);
    console.log(`%c Current In Memory data`, `color: green`);
    console.log(this.cache);
  }

  /**
   * Sets cache age
   * @param [age]
   */
  setCacheAge(age: number = 0) {
    this.maxAge = age;
  }
}
