import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { EncryptionAction } from '../utils/cache/encryption-action.enum';
import { Action } from './../models/action';
import { ServerParam } from './../models/server-param';
import { CacheKey } from './../utils/cache-key.enum';
import { CacheType } from './../utils/cache/cache-type.enum';
import { StoreType } from './../utils/cache/store-type.enum';
import { BaseConnectionService } from './base-connection.service';
import { PayloadCommandService } from './payload-command.service';
import { APIEndPoints } from '../models/api-end-points.enum';


@Injectable({
    providedIn: 'root'
})
export class ServerStatusService {

    /**
     * Creates an instance of server status service.
     * @param payloadCommandService
     * @param baseConnectionService
     */
    constructor(private payloadCommandService: PayloadCommandService,
        private baseConnectionService: BaseConnectionService) {

    }
    serverStatus = null;

    /**
     * Gets server status
     * @param serverInfo
     * @param [cacheType]
     * @returns
     */
    getServerStatus(serverInfo, actionParam?: Action): Observable<any> {
        if (!serverInfo) {
            throw new Error(`server Info can not be empty or undefined`);
        }
        if (!this.typeCheck(serverInfo)) {
            throw new Error(`Invalid server Info object`);
        }

        actionParam = !actionParam ? {} as Action : actionParam;

        actionParam.cacheType = CacheType.sticky;
        actionParam.storeType = StoreType.Session;
        actionParam.cacheKey = CacheKey.ServerKey;
        actionParam.reportError = false;
        actionParam.encryption = EncryptionAction.encrypt;
        actionParam.cmdName = ''; // reset cmd name
        actionParam.xmlRequest = '';

        const moduleInfo = {
            moduleName: 'NEWPLANTAB'
        };

        const headers = {
            'jsonRequest': 'true'
        };

        if (actionParam.endPoint) {
            actionParam.url = APIEndPoints.SERVER_STATUS;
        }

        const that = this;
        // TODO Removed hard coded values
        let serverStatusPayload;
        if (actionParam.endPoint) {
            serverStatusPayload = that.payloadCommandService.getApiRequest('getServerStatus', serverInfo, headers, moduleInfo);
            return that.baseConnectionService.send(serverStatusPayload, actionParam
                ).pipe(
                    map(function (resp) {
                        that.serverStatus = resp;
                        return that.serverStatus;
                    }));
        } else {
            serverStatusPayload = that.payloadCommandService.getCommandDetails('getServerStatus', serverInfo, null, null, moduleInfo);
            return that.baseConnectionService.sendRequest(serverStatusPayload, actionParam
                ).pipe(
                    map(function (resp) {
                        that.serverStatus = resp;
                        return that.serverStatus;
                    }));
        }
    }

    private typeCheck(obj: any): obj is ServerParam {
        return (obj.clientVersion !== undefined && obj.clientBuildID !== undefined && obj.clientBuildDate !== undefined);
    }
}
