import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { AppWindow } from '../../models/appWindow';
import { AppUtil } from '../../utils/AppUtil';
import { BASE_HREF } from '../../utils/service-constant';

@Component({
  selector: 'ctos-redirect',
  templateUrl: './redirect.component.html'
})
export class RedirectComponent implements OnInit {

  /**
   * Creates an instance of redirect component.
   * @param route
   */
  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    const queryParams: {} = this.route.snapshot.queryParams;
    let url = AppUtil.generateUrl(BASE_HREF, (this.route.snapshot.data as any).externalUrlFragment);
    let addque = true;
    for (const i in queryParams) {
      if (queryParams[i] !== undefined) {
        if (addque) {
          url += '?';
          addque = false;
        } else {
          url += '&';
        }
        url += i + '=' + queryParams[i];
      }
    }
    AppWindow.getWindow().location.href = url;
  }

}
